import L from 'leaflet'; // 下面初始化输电线路使用。
import {lineData, substationArray} from 'common/substationData';
import substationIcon from 'common/img/overlay/substationIcon.png';
/**
 * 根据相邻的4个点求当前点的值
 * @param {*} λ 经度
 * @param {*} φ 纬度
 * @param {*} grid 已有的数据点列表
 */
export function interpolate(λ, φ, grid, header, overlayName) {
  const λ0 = header.lo1;
  const φ0 = header.la1; // the grid's origin (e.g., 0.0E, 90.0N)
  const Δλ = header.dx;
  const Δφ = header.dy; // distance between grid points (e.g., 2.5 deg lon, 2.5 deg lat)
  const ni = header.nx;
  // const nj = header.ny; // number of grid points W-E and N-S (e.g., 144 x 73)

  const i = floorMod(λ - λ0, 360) / Δλ; // calculate longitude index in wrapped range [0, 360)
  const j = (φ0 - φ) / Δφ; // calculate latitude index in direction +90 to -90

  //         1      2           After converting λ and φ to fractional grid indexes i and j, we find the
  //        fi  i   ci          four points "G" that enclose point (i, j). These points are at the four
  //         | =1.4 |           corners specified by the floor and ceiling of i and j. For example, given
  //      ---G--|---G--- fj 8   i = 1.4 and j = 8.3, the four surrounding grid points are (1, 8), (2, 8),
  //    j ___|_ .   |           (1, 9) and (2, 9).
  //  =8.3   |      |
  //      ---G------G--- cj 9   Note that for wrapped grids, the first column is duplicated as the last
  //         |      |           column, so the index ci can be used without taking a modulo.

  const fi = Math.floor(i);
  const ci = fi + 1;
  const fj = Math.floor(j);
  const cj = fj + 1;

  const ciMod = ci % ni; // 不求模会导致0经度附件出现裂纹
  const fiMod = fi % ni;

  const g00 = grid[fj * ni + fiMod]; // row[fi];
  const g10 = grid[fj * ni + ciMod]; // row[ci];
  if (overlayName !== 'wind') {
    if (g00 !== undefined && g10 !== undefined) {
      const g01 = grid[cj * ni + fiMod]; // row[fi];
      const g11 = grid[cj * ni + ciMod]; // row[ci];
      if (g01 !== undefined && g11 !== undefined) {
        // All four points found, so interpolate the value.
        if (overlayName && overlayName !== 'wind') {
          return bilinearInterpolateScalar(i - fi, j - fj, g00, g10, g01, g11);
        }
        return bilinearInterpolateVector(i - fi, j - fj, g00, g10, g01, g11);
      }
    }
  }
  if (g00 && g10) {
    const g01 = grid[cj * ni + fiMod]; // row[fi];
    const g11 = grid[cj * ni + ciMod]; // row[ci];
    if (g01 && g11) {
      // All four points found, so interpolate the value.
      if (overlayName && overlayName !== 'wind') {
        return bilinearInterpolateScalar(i - fi, j - fj, g00, g10, g01, g11);
      }
      return bilinearInterpolateVector(i - fi, j - fj, g00, g10, g01, g11);
    }
  }
  if (overlayName === 'rain') {
    return 0;
  }

  // console.log("cannot interpolate: " + λ + "," + φ + ": " + fi + " " + ci + " " + fj + " " + cj);
  return null;
}

/**
 * 根据相邻的4个点求当前点的值
 * @param {*} x 横轴上当前点离上一个点的偏移量
 * @param {*} y
 * @param {*} g00 左上角点
 * @param {*} g10 右上角点
 * @param {*} g01
 * @param {*} g11
 */
function bilinearInterpolateVector(x, y, g00, g10, g01, g11) {
  const rx = 1 - x;
  const ry = 1 - y;
  const a = rx * ry;
  const b = x * ry;
  const c = rx * y;
  const d = x * y;
  const u = g00[0] * a + g10[0] * b + g01[0] * c + g11[0] * d;
  const v = g00[1] * a + g10[1] * b + g01[1] * c + g11[1] * d;
  return [u, v, Math.sqrt(u * u + v * v)];
}
/* 普通热力图数值获取方式 */
function bilinearInterpolateScalar(x, y, g00, g10, g01, g11) {
  const rx = 1 - x;
  const ry = 1 - y;
  return g00 * rx * ry + g10 * x * ry + g01 * rx * y + g11 * x * y;
}
/**
 * @returns {Number} returns remainder of floored division, i.e., floor(a / n). Useful for consistent modulo
 *          of negative numbers. See http://en.wikipedia.org/wiki/Modulo_operation.
 */
function floorMod(a, n) {
  const f = a - n * Math.floor(a / n);
  // HACK: when a is extremely close to an n transition, f can be equal to n. This is bad because f must be
  //       within range [0, n). Check for this corner case. Example: a:=-1e-16, n:=10. What is the proper fix?
  return f === n ? 0 : f;
}

function colorInterpolator(start, end) {
  const r = start[0];
  const g = start[1];
  const b = start[2];
  const Δr = end[0] - r;
  const Δg = end[1] - g;
  const Δb = end[2] - b;
  return function(i, a) {
    return [Math.floor(r + i * Δr), Math.floor(g + i * Δg), Math.floor(b + i * Δb), a];
  };
}

const τ = 2 * Math.PI;
const BOUNDARY = 0.45;
const fadeToWhite = colorInterpolator(sinebowColor(1.0, 0), [255, 255, 255]);

/**
 * Interpolates a sinebow color where 0 <= i <= j, then fades to white where j < i <= 1.
 *
 * @param i number in the range [0, 1]
 * @param a alpha value in range [0, 255]
 * @returns {Array} [r, g, b, a]
 */
export function extendedSinebowColor(i, a) {
  // return [1, 1, 1, 100];
  return i <= BOUNDARY ? sinebowColor(i / BOUNDARY, a) : fadeToWhite((i - BOUNDARY) / (1 - BOUNDARY), a);
}

/**
 * Produces a color style in a rainbow-like trefoil color space. Not quite HSV, but produces a nice
 * spectrum. See http://krazydad.com/tutorials/makecolors.php.
 *
 * @param hue the hue rotation in the range [0, 1]
 * @param a the alpha value in the range [0, 255]
 * @returns {Array} [r, g, b, a]
 */
export function sinebowColor(hue, a) {
  // Map hue [0, 1] to radians [0, 5/6τ]. Don't allow a full rotation because that keeps hue == 0 and
  // hue == 1 from mapping to the same color.
  let rad = (hue * τ * 5) / 6;
  rad *= 0.75; // increase frequency to 2/3 cycle per rad

  const s = Math.sin(rad);
  const c = Math.cos(rad);
  const r = Math.floor(Math.max(0, -c) * 255);
  const g = Math.floor(Math.max(s, 0) * 255);
  const b = Math.floor(Math.max(c, 0, -s) * 255);
  return [r, g, b, a];
}

/**
 * @returns {Number} the value x clamped to the range [low, high].
 */
function clamp(x, low, high) {
  return Math.max(low, Math.min(x, high));
}

/**
 * @returns {number} the fraction of the bounds [low, high] covered by the value x, after clamping x to the
 *          bounds. For example, given bounds=[10, 20], this method returns 1 for x>=20, 0.5 for x=15 and 0
 *          for x<=10.
 */
function proportion(x, low, high) {
  return (clamp(x, low, high) - low) / (high - low);
}

/**
 * Creates a color scale composed of the specified segments. Segments is an array of two-element arrays of the
 * form [value, color], where value is the point along the scale and color is the [r, g, b] color at that point.
 * For example, the following creates a scale that smoothly transitions from red to green to blue along the
 * points 0.5, 1.0, and 3.5:
 *
 *     [ [ 0.5, [255, 0, 0] ],
 *       [ 1.0, [0, 255, 0] ],
 *       [ 3.5, [0, 0, 255] ] ]
 *
 * @param segments array of color segments
 * @returns {Function} a function(point, alpha) that returns the color [r, g, b, alpha] for the given point.
 */
export function segmentedColorScale(segments) {
  const points = [];
  const interpolators = [];
  const ranges = [];
  for (let i = 0; i < segments.length - 1; i++) {
    points.push(segments[i + 1][0]);
    interpolators.push(colorInterpolator(segments[i][1], segments[i + 1][1]));
    ranges.push([segments[i][0], segments[i + 1][0]]);
  }

  return function(point, alpha) {
    let i;
    for (i = 0; i < points.length - 1; i++) {
      if (point <= points[i]) {
        break;
      }
    }
    const range = ranges[i];
    return interpolators[i](proportion(point, range[0], range[1]), alpha);
  };
}

export function formatVectorNew(wind) {
  const d = (Math.atan2(-wind[0], -wind[1]) / τ) * 360; // calculate into-the-wind cardinal degrees
  const wd = Math.round(((d + 360) % 360) / 5) * 5; // shift [-180, 180] to [0, 360], and round to nearest 5.
  return {
    windDirectionAngle: wd.toFixed(0),
    windSpeedNum: wind[2]
  };
}

export function formatCoordinates(λ, φ) {
  return !λ ? '' : `${Math.abs(φ).toFixed(2)}° ${φ >= 0 ? 'N' : 'S'}, ${Math.abs(λ).toFixed(2)}° ${λ >= 0 ? 'E' : 'W'}`;
}

/* 初始化输电线路效果 start */
export function initPolyline(gdMapInstance) {
  lineData.forEach((oneLineData, index) => {
    if (index > 1) {
      const coordArr = oneLineData.geometry.coordinates[0].map(latlng => latlng.reverse());
      const name = oneLineData.properties.Name ? oneLineData.properties.Name : 'undefined name';
      createPolyline(coordArr, name, gdMapInstance);
    }
  });
}

function createPolyline(latlngs, name, gdMapInstance) {
  // const latlngs = [[3, 118], [4, 119], [7, 120]];
  const polyline = L.polyline(latlngs, {color: 'red', weight: '2', bubblingMouseEvents: false})
    .addTo(gdMapInstance)
    .bindPopup(name);
  // zoom the map to the polyline
  polyline.on('mouseover', () => {
    polyline.setStyle({color: 'rgb(50, 235, 170)', weight: '3'}).openPopup();
  });
  polyline.on('mouseout', () => {
    polyline.setStyle({color: 'red', weight: '2'}).closePopup();
  });
}
/* 初始化输电线路效果 end */
/* 初始化变电站位置效果 start */
export function initSubstation(gdMapInstance) {
  substationArray.forEach((sub) => {
    const coordinates = sub.geometry.coordinates.reverse();
    const name = sub.properties.Name;
    createSubstation(coordinates, name, gdMapInstance);
  });
}
function createSubstation(latlng, name, gdMapInstance) {
  const icon = L.icon({
    iconUrl: substationIcon,
    iconSize: [16, 16],
    iconAnchor: [8, 8],
    popupAnchor: [8, 8]
  });
  L.marker(latlng, {icon, title: name}).addTo(gdMapInstance);
}
/* 初始化变电站位置效果 end */
