import {device} from '@enos/envhybrid-utils';
import React from 'react';
import {toJS} from 'mobx';
import {withRouter} from 'react-router-dom';
import './index.css';
import './iosMatch.less';
import 'common/mapIcon/style.css';
import {Tree} from 'antd';
import {SearchBar} from 'antd-mobile';
import Scroll from 'react-bscroll';
import locationIcon from 'common/img/searchSite/location.png';
import {getStorageUnparse, setStorage, removeStorage, historyPush, getSiteId} from 'common/utils/utils';
import {editAttributeFn, getAppId, language} from 'common/Utils';
import i18n from 'common/i18n';
import Message from 'components/MessageTip';
import constants from 'common/utils/constants';
// import {farmList} from './mock';
import {post} from 'common/utils/request';
import uris from 'common/utils/uris';
import weatherStore from '../Weather/models/WeatherStore.js';

// document.cookie = 'global_id=SID_W_1567650816925_60155b03-3120-4dad-bea0-eedc60a92e90';

const lan = language.indexOf('en') > -1;
const addressHistory = lan ? `addressHistoryEn${constants.mode}` : `addressHistory${constants.mode}`;

const TreeNode = Tree.TreeNode;
class SearchSite extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      curTab: 0,
      searchValue: '',
      arrayDataForAddress: [],
      selectedNode: '',
      farmList: [],
      expandedKeys: ['/'],
      message: {
        contont: '',
        visible: false
      }
    };
    //   this.autoResize = this._autoResize.bind(this);
    this.toSearch = this._toSearch.bind(this);
    this.linkToWeatherSetStatus = this.linkToWeatherSetStatus.bind(this);
  }

  componentDidUpdate() {
    this.replaceTreeIon();
  }


  replaceTreeIon() {
    // 按ue设计需要替换树的+ - 图标
    // console.log(Array.from(document.getElementsByClassName('ant-tree-switcher ant-tree-switcher_close')));
    Array.from(document.getElementsByClassName('ant-tree-switcher ant-tree-switcher_close')).forEach((o) => {
      o.className = 'treeIcon map_plus_square_s';
    });
    Array.from(document.getElementsByClassName('ant-tree-switcher ant-tree-switcher_open')).forEach((o) => {
      o.className = 'treeIcon map_minus_square_s';
    });
  }

  componentDidMount() {
    // var storage=window.localStorage;
    // if (storage[addressHistory] !==undefined && storage[addressHistory].length !== 0) {
    //   var historyArr = storage[addressHistory].split(',');
    //   this.setState({
    //     arrayDataForAddress: historyArr
    //   })
    // }
    // 获取历史记录
    this.getAdderssHistory();
    post(uris.getNewTree(), {appId: getAppId()})
      .then((res) => {
        this.setState({
          farmList: res.data,
          expandedKeys: [...getSiteId([res.data])]
        });
        setStorage('kongmingFarmList', res.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }

  switchTab(num) {
    this.setState({
      curTab: num
    });
  }

  // 搜索框获取焦点跳转至搜索页面
  _toSearch() {
    if (this.state.curTab === 0) {
      // getStorage('fromEarth').then((data) => {
      //   if (data) { // 从earth中进入的
      //     // historyPush('/eos-fc-map/index.html#/AddressList');
      //     console.log('进入搜索也');
      //     historyPush('/eos-fc-map/index.html#/AddressList');
      //     // nativeHistory.push('/eos-fc-map/index.html#/AddressList')
      //   } else {
      //     this.props.history.push('/AddressList');
      //   }
      // });
      this.props.history.push(`/AddressList/${constants.mode}`);
    } else {
      this.props.history.push(`/FarmList/${constants.mode}`);
    }
  }

  onSelect = (selectedKeys) => {
    // console.log('selected', selectedKeys, info);
    let {expandedKeys} = this.state;
    const clickKey = selectedKeys[0] || this.state.selectedNode;
    const index = expandedKeys.indexOf(clickKey);
    if (index === -1) {
      // 该节点展开
      expandedKeys.push(clickKey);
    } else {
      expandedKeys = expandedKeys.filter(ele => !ele.includes(clickKey));
    }
    this.setState({
      selectedNode: selectedKeys[0],
      expandedKeys
    }, () => {
      if (this.state.selectedNode === '' || !this.state.selectedNode) {
        return false;
      }
      post(uris.getFarmDetail(), {assetIds: selectedKeys}).then((res) => {
        if (res.data.length) {
          const farmItem = res.data[0];
          let name;
          if (lan) { // 英文环境
            name = farmItem.name.i18nValue.en_US || farmItem.name.i18nValue.zh_CN || farmItem.name.defaultValue;
          } else {
            name = farmItem.name.i18nValue.zh_CN || farmItem.name.i18nValue.en_US || farmItem.name.defaultValue;
          }
          const SelectedFarm = {lat: farmItem.attributes.latitude, lon: farmItem.attributes.longitude, name, addr: name};
          this.linkToWeatherSetStatus(SelectedFarm, 'FARM');
        }
      }).catch((e) => {
        console.log(e);
        this.showMessage('请选择有效站点');
      });
      // try {
      //   const confirmFarm = this.state.farmList.map(f => f.attributes).filter(d => d.objectID === this.state.selectedNode)[0];
      //   console.log(confirmFarm);
      //   if (confirmFarm && confirmFarm.longitude) {
      //     // const SelectedFarm = {lat: confirmFarm.latitude, lon: confirmFarm.longitude, name: confirmFarm.name, addr: confirmFarm.name};
      //     // this.linkToWeatherSetStatus(SelectedFarm, 'FARM');
      //     // this.props.history.push(`/weather/${constants.mode}`);
      //     // getStorage('fromEarth').then((data) => {
      //     //   if (data) { // 从earth中进入的
      //     //     setStorage('fromAddrFc', confirmFarm).then(() => {
      //     //       historyPush('/eos-fc-map/screen.html');
      //     //     });
      //     //   } else {
      //     //     setStorage('fromFarmFc', confirmFarm).then(() => {
      //     //       historyPush('/eos-fc-map/index.html#/windmap/0/0/farm');
      //     //     });
      //     //   }
      //     // });
      //   } else {
      //     // alert('请选择有经纬度的站点');
      //     this.showMessage('请选择有效站点');
      //   }
      // } catch (e) {
      //   console.log(e);
      // }
    });
  }

  onExpend = (expandedKey, obj) => {
    // console.log('expend', expandedKey, obj);
    let {expandedKeys} = this.state;

    // 展开的状态
    if (obj.expanded) {
      this.setState({
        expandedKeys: expandedKey
      });
    } else {
      // expandedKey 返回的是当前已经展开的元素 expandedKeys 是上次展开的元素
      // 比较两个数组中缺少的元素得出当前被收起的是哪个元素
      const removeArray = this.diffArray(expandedKey, expandedKeys);
      // 收起的时候需要把里面展开的元素一并移除，不然会造成收起动作无效
      expandedKeys = expandedKeys.filter(ele => !ele.includes(removeArray[0]));
      this.setState({
        expandedKeys
      });
    }
  }

  // 比较出2个数组中不一样的元素
  diffArray = (arr1, arr2) => {
    const arr3 = [];
    for (let i = 0; i < arr1.length; i++) {
      if (arr2.indexOf(arr1[i]) === -1) { arr3.push(arr1[i]); }
    }
    for (let j = 0; j < arr2.length; j++) {
      if (arr1.indexOf(arr2[j]) === -1) { arr3.push(arr2[j]); }
    }

    // console.log("diff ", arr3)
    return arr3;
  }

  expandTree = () => {
    this.scrollObj = this.scrollFarm.getScrollObj();
    this.scrollObj.refresh();
    // console.log('refresh Scroll');
  }

  onConfirm() {
    if (this.state.selectedNode === '' || !this.state.selectedNode) {
      return false;
    }
    // 跳转 this.state.selectedNode 当前选中的节点
    // console.log(this.state.selectedNode);
    try {
      const confirmFarm = this.state.farmList.map(f => f.attributes).filter(d => d.objectID === this.state.selectedNode)[0];
      // console.log(confirmFarm);
      if (confirmFarm && confirmFarm.longitude) {
        setStorage('fromFarmFc', confirmFarm).then(() => {
          historyPush('/eos-fc-map/index.html#/windmap/0/0/farm');
        });
      } else {
        // alert('请选择有经纬度的站点');
        this.showMessage('请选择有效站点');
      }
    } catch (e) {
      // console.log(e);
    }


    // if (device.isMobile) {
    //     setStorage('fromFarmFc',this.state.selectedNode);
    //     historyPush('/eos-fc-map/index.html#/windmap/0/0/farm');
    //     // sharedDataApi.saveShellPersistentData('fromFarmFC', JSON.stringify(this.state.selectedNode)).then(()=>{
    //     //   nativeHistory.push('/eos-fc-map/index.html#/windmap/0/0/farm');
    //     // });
    //   } else {
    //     window.location.href = `/eos-fc-map/index.html#/windmap/0/0/farm`;
    //   }
  }

  showMessage = (msg) => {
    this.setState({
      message: {
        content: msg,
        visible: true
      }
    }, () => {
      setTimeout(() => this.setState({
        message: {
          content: '',
          visible: false
        }
      }), constants.MESSAGE_DURATION * 500);
    });
  }

  getAdderssHistory() {
    getStorageUnparse(addressHistory).then((historyResult) => {
      if (historyResult !== undefined && historyResult.length !== 0) {
        const historyArr = historyResult.split('##');
        this.setState({
          arrayDataForAddress: historyArr
        });
      } else {
        this.setState({
          arrayDataForAddress: []
        });
      }
    });
    // const storage = window.localStorage;
    // if (storage[addressHistory] !== undefined && storage[addressHistory].length !== 0) {
    //   const historyArr = storage[addressHistory].split('##');
    //   console.log('get address history: ', historyArr);
    //   this.setState({
    //     arrayDataForAddress: historyArr
    //   });
    // } else {
    //   this.setState({
    //     arrayDataForAddress: []
    //   });
    // }
  }

  clearHistory() {
    // console.log('remove address history');
    removeStorage(addressHistory);
    // console.log('success remove address history');
    // sharedDataApi.removeShellPersistentData('addressHistory');
    this.getAdderssHistory();
  }

  toLInk(detailAdd) {
    // console.log(detailAdd);
    const detail = detailAdd.split('&');
    const historyAddr = {name: detail[0], addr: detail[1], lat: detail[2], lon: detail[3]};
    this.linkToWeatherSetStatus(historyAddr);
    // getStorage('fromEarth').then((data) => {
    //   if (data) { // 从earth中进入的
    //     setStorage('fromAddrFc', historyAddr).then(() => {
    //       // console.log('fromAddrFc: ', historyAddr);
    //       historyPush('/eos-fc-map/screen.html');
    //     });
    //   } else {
    //     setStorage('fromAddrFc', historyAddr).then(() => {
    //       // console.log('fromAddrFc: ', historyAddr);
    //       historyPush('/eos-fc-map/index.html#/windmap/0/0/addr');
    //     });
    //   }
    // });
  }

  goBack = () => {
    this.props.history.goBack();
    // getStorage('fromEarth').then((data) => {
    //   if (data) { // 从earth中进入的
    //     historyPush('/eos-fc-map/screen.html');
    //   } else {
    //     // this.props.history.goBack();
    //     historyPush('/eos-fc-map/index.html#/windmap/0/0/map');
    //     // historyPush('/eos-fc-map/index.html#/windmap/0/0/addr');
    //   }
    // });
  }

  getFarmTree = (data) => {
    if (!data || data.length === 0) {
      return <TreeNode title='当前用户无场站;或登录过期,请重新登录' />;
    }
    return this.loop(data);
  }

  loop = data => data.map((item) => {
    if (item.children && item.children.length) {
      return <TreeNode key={item.objectID} title={item.name}>{this.loop(item.children)}</TreeNode>;
    }
    return <TreeNode key={item.objectID} title={item.name} />;
  });

  linkToWeatherSetStatus(historyAddr, flag) {
    if (!historyAddr.lat && !historyAddr.lon) { // 经纬度都不存在
      this.showMessage(`${lan ? 'Invalid longitude and latitude' : '经纬度无效'}`);
      return;
    }
    if (flag === 'FARM') {
      this.props.weatherStore.setFromFarmStatus(true);
    } else {
      this.props.weatherStore.setFromSearchStatus(true);
    }
    this.props.weatherStore.setSelectedLocation(historyAddr);
    this.props.weatherStore.setShowInfoPanelStatus(1);
    const newInfo = Object.assign(toJS(this.props.weatherStore.currentInfo), {});
    const lonLat = {lat: historyAddr.lat, lng: historyAddr.lon};
    newInfo.hideOverlayTip = false;
    editAttributeFn('coordinates.latLng', lonLat, newInfo);
    this.props.weatherStore.setCurrentInfo(newInfo);
    this.props.history.push(`/weather/${constants.mode}`);
  }

  // 生成场站树数据
  getTreeData(data) {
    if (!data[0].id) {
      return [];
    }
    const treeData = [];
    // const count = 0;
    if (data) {
      data.forEach((element) => {
        if (element) {
          if (element.children && element.children.length > 0) {
            const children = this.getTreeData(element.children);
            treeData.push({
              title: element.name,
              key: element.id,
              children
            });
          } else {
            treeData.push({
              title: element.name,
              key: element.id
            });
          }
        }
      });
    }
    return treeData;
  }

  // 递归生成树结构
  renderTreeNodes = (data) => {
    if (data.length) {
      return data.map((item) => {
        if (item.children) {
          return (
            <TreeNode title={item.title} key={item.key} dataRef={item}>
              {this.renderTreeNodes(item.children)}
            </TreeNode>
          );
        }
        return <TreeNode key={item.key} {...item} />;
      });
    }
    return <TreeNode key='null' />;
  }

  render() {
    const addressList = this.state.arrayDataForAddress;
    const areaList = [];
    /* eslint-disable */
    if (addressList.length !== 0) {
      for (let i = 0; i < addressList.length; i++) {
        const item = addressList[i];
        const tempList = (
          <li key={i} onClick={() => this.toLInk(item)}>
            <p className='title'>{item.split('&')[0]}</p>
            <p className='detail'>{item.split('&')[1]}</p>
            <img src={locationIcon} alt='locationIcon' className='locationIcon' />
          </li>
        );
        areaList.push(tempList);
      }
      const clearItem = (
        <li className='clearAddHostory' key='clear' onClick={() => this.clearHistory()}>
          <p className='text'>
            <span className='map_delete deleteIcon' />
            {i18n.CLEAR_HISTORY}
          </p>
        </li>
      );
      areaList.push(clearItem);
    }
    /* eslint-disable */
    const {message} = this.state;
    return (
      <div className={`searchSite_container${device.isiOS ? ' ios' : ''}`}>
        <Message message={message.content} visible={message.visible} />
        <div className='wrongArea'>
          <span className='map_wrong wrongIcon' onClick={this.goBack} />
        </div>
        <div className='tabArea'>
          <div className={['tabItem', this.state.curTab === 0 ? 'active' : null].join(' ')} onClick={() => { this.switchTab(0); }}>
            <p className='tabText'>{i18n.SEARCH_LOCATION}</p>
            <span className='curCss' />
          </div>
          <div className={['tabItem', this.state.curTab === 1 ? 'active' : null].join(' ')} onClick={() => { this.switchTab(1); }}>
            <p className='tabText'>{i18n.SEARCH_FARMS}</p>
            <span className='curCss' />
          </div>
        </div>
        <div className='searchArea searchfarm' onClick={this.toSearch}>
          <SearchBar
            placeholder={this.state.curTab === 0 ? i18n.PLEASE_ENTER_THE_LOCATION : i18n.PLEASE_ENTER_THE_SITE}
            disabled
            value={this.state.searchValue}
            onFocus={this.toSearch}
          />
        </div>
        {/* 位置查找 */}
        <div className={['locationTab', this.state.curTab === 0 ? null : 'hidden'].join(' ')}>
          <Scroll
            ref={(scrollAddr) => { this.scrollAddr = scrollAddr; }}
            click
            scrollbar={false}
          >
            <ul className='historyAddress'>
              {areaList}
            </ul>
          </Scroll>
        </div>
        {/* 场站查找 */}

        <div className={['farmTab', this.state.curTab === 1 ? null : 'hidden'].join(' ')}>
          <div className='treeArea'>
            <Scroll
              ref={(scrollFarm) => { this.scrollFarm = scrollFarm; }}
              click
              scrollbar={false}
            >
              <Tree
                showLine
                defaultExpandAll
                onSelect={this.onSelect}
                onExpand={this.onExpend}
                expandedKeys={this.state.expandedKeys}

              >
                {/* {this.getFarmTree(setTreeData(this.state.farmList))} */}
                {this.renderTreeNodes(this.getTreeData([this.state.farmList]))}
                {/* <TreeNode title="全国区域" key="0-0">
                                <TreeNode title="华东区域" key="0-0-0">
                                    <TreeNode title="CHN_R_WF01" key="0-0-0-0">
                                        <TreeNode title="CHN_R_WF01" key="0-0-0-0-1" />
                                        <TreeNode title="CHN_R_WF01" key="0-0-0-0-2" />
                                    </TreeNode>
                                    <TreeNode title="CHN_R_WF01" key="0-0-0-1" />
                                    <TreeNode title="CHN_R_WF01" key="0-0-0-2" />
                                </TreeNode>
                                <TreeNode title="华南区域" key="0-0-1">
                                    <TreeNode title="CHN_R_WF01" key="0-0-1-0" />
                                </TreeNode>
                                <TreeNode title="华北区域" key="0-0-2">
                                    <TreeNode title="CHN_R_WF01" key="0-0-2-0" />
                                    <TreeNode title="CHN_R_WF01" key="0-0-2-1" />
                                </TreeNode>
                            </TreeNode> */}
              </Tree>
            </Scroll>
          </div>
          {/* <div className="confirmArea"> */}
          {/* <p className="btn cancel" onClick={this.goBack}>{i18n.CANCEL}</p> */}
          {/* <p className="btn confirm" onClick={() => this.onConfirm()}>{i18n.CONFIRM}</p> */}
          {/* </div> */}
        </div>

      </div>
    );
  }
}

export default withRouter(props => <SearchSite {...props} weatherStore={weatherStore} />);