/**
 * 随机一个key出来，防止百度或者高德的单个key达到访问上限
 * @param {key的数组} res
 */
function _genRandomMapAK(res) {
  // let res = ['4DGyGUFNGXkwZYhr34H5t6sc0duah5gi'];
  const index = Math.floor(Math.random() * res.length);
  return res[index];
}
