import React from 'react';
import {HashRouter, Route, Switch} from 'react-router-dom';
import Login from './pages/Login';
import Weather from './pages/Weather';
import SearchSite from './pages/SearchSite';
import FarmList from './pages/FarmList';
import AddressList from './pages/AddressList';

const BasicRoute = () => (
  <HashRouter>
    <Switch>
      <Route path='/login' component={Login} />
      {/* SARAWAK SINGAPORE GLOBAL */}
      <Route path='/weather/:mode' exact component={Weather} />
      <Route path='/SearchSite/:mode' component={SearchSite} />
      <Route path='/FarmList/:mode' component={FarmList} />
      <Route path='/addressList/:mode' component={AddressList} />
    </Switch>
  </HashRouter>
);

export default BasicRoute;
