import React from 'react';
import 'common/css/common.css';
import './index.css';
import {setCookieDefalutExpires, setCookie} from 'common/Utils';
import {pageMsg} from 'common/Locale';
import {GLOBAL_ID_KEY} from 'common/config/constants';
import message from 'components/Message';

const loginMsg = pageMsg('LOGIN');

const classPrefix = 'login-container';
const user = {
  username: 'system',
  password: 'test'
};

class Login extends React.Component {
  constructor() {
    super();

    this.state = {
      username: '',
      password: ''
      // backurl: qs.parse(window.location.search.substr(1)).backurl || '.',
    };

    this._handleKeyDown = this._handleKeyDown.bind(this);
  }

  componentDidMount() {
    document.addEventListener('keydown', this._handleKeyDown);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this._handleKeyDown);
  }

  _handleKeyDown(e) {
    if (e.keyCode === 13) {
      this._handleValidUser();
    }
  }

  _handleValidUser() {
    const {username, password} = this.state;
    if (username && password) {
      if (username.trim() === user.username && password.trim() === user.password) {
        this.loginSuccess();
      } else {
        message.warn('用户名或者密码错误');
      }
    } else {
      message.warn('用户名和密码不能为空');
    }
  }

  loginSuccess() {
    setCookieDefalutExpires(GLOBAL_ID_KEY, Date.now());
    window.location.hash = '#';
  }

  //   _encryptPwd(pwd){
  //     return CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(pwd),
  //           CryptoJS.enc.Utf8.parse('sfsafasdfsafasff'),
  //           {
  //             'iv': CryptoJS.enc.Utf8.parse('phjlknslfdhksldf'),
  //             'mode': CryptoJS.mode.CBC
  //           }).toString();
  //   }

  _getLangStyle(lang) {
    if (this.state.locale === lang) { return 'lang-choose-active'; }

    return 'lang-choose';
  }

  renderLangSwitch() {
    return (
      <span
        className='lang'
        onClick={(evt) => {
          const lang = evt.target.getAttribute('data-value');
          if (!lang) return;
          setCookie('lang', lang, '/hmi');

          this.setState({
            locale: lang
          // 'i18n': require(`localization/${lang}.json`)
          });
        }}
      >
        <span data-value='en-US' className={this._getLangStyle('en-US')}>En</span>
        <span> / </span>
        <span data-value='zh-CN' className={this._getLangStyle('zh-CN')}>简体中文</span>
      </span>
    );
  }

  renderFindPassword() {
    return (
      <div className='findpwd'>
        <span onClick={() => { document.location.href = './resetpwd.html'; }}>{this.state.i18n.FORGET_PWD}</span>
      </div>
    );
  }

  render() {
    const copyRight = `Copyright© 2012-${new Date().getFullYear()} 远景能源 保留所有权利`;

    return (
      <div className={classPrefix}>
        <div>
          <div className='logo-container'>
            <img alt='404' src={require('common/img/hmi_logo.png')} className='logo' />
          </div>
          <div className='login'>
            <div className='form'>
              <span className='label'>用户名</span>
              <div className='user'>
                <input
                  type='text'
                  value={this.state.username}
                  onChange={e => this.setState({username: e.target.value})}
                />
              </div>

              <span className='label'>密码</span>
              <div className='pwd'>
                <input
                  type='password'
                  value={this.state.password}
                  onChange={e => this.setState({password: e.target.value})}
                />
              </div>
              <button className='submit' onClick={() => this._handleValidUser()}>
                {loginMsg('BTN_LOGIN')}
              </button>
            </div>
          </div>
        </div>
        <div className='copy'>{copyRight}</div>
        <img alt='404' src={require('./login-bg-logo.png')} className='logo-bg' />
      </div>
    );
  }
}

export default Login;
