import {device} from '@enos/envhybrid-utils';
import React, {Component} from 'react';
import {toJS} from 'mobx';
import {withRouter} from 'react-router-dom';
import {SearchBar} from 'antd-mobile';
import Message from 'components/MessageTip';
import './index.css';
import './iosMatch.less';
import 'common/mapIcon/style.css';
// import Utils from '../utils/util';
// import {mockFarm} from './mock';
import Scroll from 'react-bscroll';
import {getStorageUnparse, setStorageUnSty, getStorage, historyBack, removeStorage, getSiteList} from 'common/utils/utils';
import constants from 'common/utils/constants';
import {editAttributeFn, language} from 'common/Utils';
import {post} from 'common/utils/request';
import uris from 'common/utils/uris';
import i18n from 'common/i18n';
import weatherStore from '../Weather/models/WeatherStore.js';

// const Item = List.Item;
// const Brief = Item.Brief;
// const isFocus = Utils.getQueryString('href');
// const historyItem = Utils.getQueryString('historyItem');

const farmHistory = 'farmHistory'; // 存储localStorage的key
const historyItem = undefined;
const lan = language.indexOf('en') > -1;
class FarmList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchValue: '',
      focused: false,
      arrayDataForFarm: [],
      message: {
        contont: '',
        visible: false
      }
    };
    this.itemClick = this._itemClick.bind(this);
    this.loadAllItemClick = this._loadAllItemClick.bind(this);
    this.search = this._search.bind(this);
    this.onCancel = this._onCancel.bind(this);
    this.onClear = this._onClear.bind(this);
    this.onFocus = this._onFocus.bind(this);
    this.onBlur = this._onBlur.bind(this);
    this.linkToWeatherSetStatus = this.linkToWeatherSetStatus.bind(this);
    document.addEventListener('navileft', this.onNaviLeft.bind(this), false);
  }

  componentDidMount() {
    // const isFocus = Utils.getQueryString('href')
    // var storage=window.localStorage;
    // if (storage["farmHistory"] !==undefined && storage["farmHistory"].length !== 0) {
    //   var historyArr = storage["farmHistory"].split(',');
    //   this.setState({
    //     arrayDataForFarm: historyArr
    //   })
    // }
    this.getFarmHistory();
    this.getSiteInfosData();
    this.initPage();
  }

  initPage() {
    // if (isFocus !== undefined && isFocus === 'focus') { // 获取输入焦点
    // }
    if ((historyItem !== undefined && historyItem !== '') && historyItem !== null) {
      setTimeout(() => {
        this.setState({
          searchValue: historyItem
        });
      }, 20);
    }
  }

  onNaviLeft() {
    historyBack();
  }

  getSiteInfosData() {
    // const sites = mockFarm.filter((data) => {
    //   return data.attributes.objectTypeID === '102';
    // })
    // console.log(sites)
    // this.setState({ sites });

    // 异步方法
    getStorage('kongmingFarmList').then((farmList) => {
      const sites = getSiteList(farmList);
      const obj = {};
      for (let x = 0; x < sites.length; x++) {
        obj[sites[x].id] = sites[x];
      }
      const promiseArr = sites.map(item => post(uris.getFarmDetail(), {assetIds: [`${item.id}`]}));
      Promise.all(promiseArr).then((res) => {
        for (let i = 0; i < res.length; i++) {
          const cur = res[i].data[0];
          obj[cur.assetId].capacity = cur.attributes.capacity || 0;
          obj[cur.assetId].latitude = cur.attributes.latitude || null;
          obj[cur.assetId].longitude = cur.attributes.longitude || null;
        }
      });
      // const sites = farmList.filter(data => data.attributes.objectTypeID === '102' && data.attributes.businessType.indexOf('21') > -1);
      this.setState({sites});
    });
  }

  _itemClick(name, masterId) {
    // if (this.state.searchValue === '') {
    //   // 直接跳转
    //   this.toLink(masterId);
    // } else {
    //   let historyParam = `${this.state.searchValue}&${masterId}`
    //   sharedDataApi.saveShellPersistentData('farmHistory', historyParam);
    //   this.toLink(masterId);
    // }
    if (this.state.searchValue === '') {
      // 直接跳转
      this.toLink(masterId);
    } else {
      // 是进行了搜索操作
      const historyParam = `${name}&${masterId}`;
      // 使用工具函数进行存储
      if (historyParam !== '') {
        // 写入a字段
        let isHave = false;
        getStorageUnparse(farmHistory).then((historyResult) => {
          if (historyResult !== undefined) {
            historyResult.split(',').forEach((n) => {
              if (n === historyParam) {
                isHave = true;
              }
            });
          } else {
            setStorageUnSty(farmHistory, '');
          }
          if (!isHave) {
            if (historyResult !== undefined) {
              if (historyResult !== '') {
                setStorageUnSty(farmHistory, `${historyResult},${historyParam}`);
              } else {
                setStorageUnSty(farmHistory, historyParam);
              }
            } else {
              setStorageUnSty(farmHistory, historyParam);
            }
          }
        });
      }
      // 用本地的localStorage进行存储
      // if (!window.localStorage) {
      //   alert('浏览器不支持localstorage');
      //   return false;
      // }
      // if (historyParam !== '') {
      //   const storage = window.localStorage;
      //   // 写入a字段
      //   let isHave = false;
      //   if (storage.farmHistory !== undefined) {
      //     storage.farmHistory.split(',').forEach((n) => {
      //       if (n === historyParam) {
      //         isHave = true;
      //       }
      //     });
      //   } else {
      //     storage.farmHistory = [];
      //   }
      //   if (!isHave) {
      //     if (storage.farmHistory !== undefined) {
      //       if (storage.farmHistory !== '') {
      //         storage.farmHistory += `,${historyParam}`;
      //       } else {
      //         storage.farmHistory = historyParam;
      //       }
      //     } else {
      //       storage.farmHistory = historyParam;
      //     }
      //   }
      // }
      this.toLink(masterId);
    }
  }

  _loadAllItemClick() {
    this.setState({searchValue: ''});
  }

  _search(searchValue) {
    // console.log(this.state.focused)
    // console.log(this.state.searchValue)
    this.setState({searchValue});
  }

  _onClear() {
    this.setState({
      searchValue: '',
      focused: false
    });
    this._loadAllItemClick();
  }

  _onCancel() {
    this.setState({focused: false});
    this.props.history.goBack();
  }

  _onFocus() {
    this.setState({focused: true});
  }

  _onBlur() {
    this.setState({focused: false});
  }

  handleClick() {
    // 使用原生的 DOM API 获取焦点
    // this.refs.searchFocus.click();
    // document.getElementById("sear").click()
    document.getElementsByClassName('am-search-value')[0].focus();
    // alert('2')
  }

  toLink(masterId) {
    const confirmFarm = this.state.sites.filter(d => d.id === masterId)[0];
    // console.log(confirmFarm);
    this.linkToWeatherSetStatus(confirmFarm);
    // getStorage('fromEarth').then((data) => {
    //   if (data) { // 从earth中进入的
    //     setStorage('fromAddrFc', confirmFarm).then(() => {
    //       // console.log('fromAddrFc: ', historyAddr);
    //       historyPush('/eos-fc-map/screen.html');
    //     });
    //   } else {
    //     setStorage('fromFarmFc', confirmFarm).then(() => {
    //       // console.log('fromAddrFc: ', historyAddr);
    //       historyPush('/eos-fc-map/index.html#/windmap/0/0/farm');
    //     });
    //   }
    // });
  }

  getFarmHistory() {
    getStorageUnparse(farmHistory).then((historyResult) => {
      if (historyResult !== undefined && historyResult.length !== 0) {
        const historyArr = historyResult.split(',');
        this.setState({
          arrayDataForFarm: historyArr
        });
      } else {
        this.setState({
          arrayDataForFarm: []
        });
      }
    });
    // const storage = window.localStorage;
    // if (storage.farmHistory !== undefined && storage.farmHistory.length !== 0) {
    //   const historyArr = storage.farmHistory.split(',');
    //   this.setState({
    //     arrayDataForFarm: historyArr
    //   });
    // } else {
    //   this.setState({
    //     arrayDataForFarm: []
    //   });
    // }
  }

  clearHistory() {
    // console.log('remove farm history');
    removeStorage(farmHistory);
    // sharedDataApi.removeShellPersistentData('farmHistory');
    this.getFarmHistory();
  }

  showMessage = (msg) => {
    this.setState({
      message: {
        content: msg,
        visible: true
      }
    }, () => {
      setTimeout(() => this.setState({
        message: {
          content: '',
          visible: false
        }
      }), constants.MESSAGE_DURATION * 500);
    });
  }

  linkToWeatherSetStatus(confirmFarm) {
    const weatherStoreCopy = this.props.weatherStore;
    const SelectedFarm = {lat: confirmFarm.latitude, lon: confirmFarm.longitude, name: confirmFarm.name, addr: confirmFarm.name};
    if (!SelectedFarm.lat && !SelectedFarm.lon) { // 经纬度都不存在
      this.showMessage(`${lan ? 'Invalid longitude and latitude' : '经纬度无效'}`);
      return;
    }
    weatherStoreCopy.setFromFarmStatus(true);
    weatherStoreCopy.setSelectedLocation(SelectedFarm);
    weatherStoreCopy.setShowInfoPanelStatus(1);
    const newInfo = Object.assign(toJS(weatherStoreCopy.currentInfo), {});
    const lonLat = {lat: confirmFarm.latitude, lng: confirmFarm.longitude};
    newInfo.hideOverlayTip = false;
    editAttributeFn('coordinates.latLng', lonLat, newInfo);
    weatherStoreCopy.setCurrentInfo(newInfo);
    this.props.history.push(`/weather/${constants.mode}`);
  }

  render() {
    let sites = this.state.sites || [];
    sites = sites
      .filter(o => o.name.indexOf(this.state.searchValue) !== -1);
    const siteItems = [];
    for (let i = 0; i < sites.length; i += 1) {
      const site = sites[i];
      if (site) {
        let capacity = '';
        if (site.capacity) {
          capacity = `${i18n.CAPACITY}：${site.capacity}MW`;
        }
        // let randomColor = '';
        // if (parseInt(Math.random() * 10, 10) > 7.5) {
        //   randomColor = 'dotRed';
        // } else if (parseInt(Math.random() * 10, 10) > 5) {
        //   randomColor = 'dotGreen';
        // } else if (parseInt(Math.random() * 10, 10) > 2.5) {
        //   randomColor = 'dotYellow';
        // } else if (parseInt(Math.random() * 10, 10) >= 0) {
        //   randomColor = 'dotPurple';
        // }
        /* eslint-disable */
        const item = (
          <li className='item' onClick={() => this.itemClick(site.name, site.id)} key={site.id}>
            <p className='name'>
              <span className='map_right_s rightIcon' />
              {/* <span className={["dot", randomColor].join(' ')}></span> */}
              {site.name}
            </p>
            <p className='desc'>
              <span className='unit'>{capacity}</span>
              {/* <span className="unit">满发：545Hr</span> */}
            </p>
            {/* <img className="arrowIcon" src={arrowIcon}/> */}
          </li>
        );
        /* eslint-disable */
        siteItems.push(item);
      }
    }
    //  去掉 ‘加载全部。。。’文案
    // if (this.state.searchValue) {
    //   siteItems.push(<Item
    //     multipleLine
    //     value={this.state.searchValue}
    //     key='loadItem'
    //     onClick={
    //     () => this.loadAllItemClick()}
    //   > {i18n.LOAD_ALL}...
    //   </Item>);
    // }
    // 历史记录显示 ----------------------------------------------------------------
    const farmList = this.state.arrayDataForFarm;
    const areaList = [];
    for (let i = 0; i < farmList.length; i++) {
      const name = farmList[i].split('&')[0];
      const master = farmList[i].split('&')[1];
      /* eslint-disable */
      const template = (
        <li className='item' onClick={() => this.toLink(master)} key={i}>
          <span className='map_time_circle_s timeIcon' />
          {name}
        </li>
      );
      /* eslint-disable */
      areaList.push(template);
    }
    const {message} = this.state;
    return (
      <div className={`farmlist${device.isiOS ? ' ios' : ''}`}>
        <Message message={message.content} visible={message.visible} />
        <div className='searchArea'>
          <SearchBar
            showCancelButton
            placeholder={i18n.SEARCH_FARMS}
            value={this.state.searchValue}
            onChange={this.search}
            onClear={this.onClear}
            onCancel={this.onCancel}
            onFocus={this.onFocus}
            // onBlur={this.onBlur}
            cancelText={i18n.CANCEL}
            focused={this.state.focused}
            id='sear'
          />
        </div>
        <div className={['history', (!this.state.focused && this.state.searchValue === '') ? null : 'hidden'].join(' ')}>
          <div className='deleteIconArea'>
            <span className='map_delete deleteIcon' onClick={() => this.clearHistory()} />
          </div>
          <ul className='historyBox'>
            {areaList}
          </ul>
        </div>
        <div className={['result', (this.state.focused || this.state.searchValue !== '') ? null : 'hidden'].join(' ')}>
          {this.state.searchValue === '' || (siteItems && siteItems.length > 0) ? (
            <Scroll
              click
              scrollbar={false}
            >
              <ul className='list'>
                {siteItems}
              </ul>
            </Scroll>
          )
            : (
              <div className='search-notfound'>
                <i className='icon' />
                <div className='noSearchContent'>{i18n.NOSEARCHRESULT}</div>
                <div className='inputAgain'>{i18n.INPUTAGAIN}</div>
              </div>
            )
          }
        </div>
      </div>
    );
  }
}

export default withRouter(props => <FarmList {...props} weatherStore={weatherStore} />);
