import {device} from '@enos/envhybrid-utils';
import {fetchData} from 'common/Utils';
import constants from 'common/utils/constants';
import weatherStore from './models/WeatherStore';
import {WIND_OVERLAY, TEMP_OVERLAY, HUMIDITY_OVERLAY, RAIN_OVERLAY} from './constants';

// function mathRound(v, pointNum) {
