function getParamsUrl(overlayName) {
  switch (overlayName) {
  case WIND_OVERLAY:
    return ['U100', 'V100'];
  case TEMP_OVERLAY:
    return 'T2';
  case HUMIDITY_OVERLAY:
    return 'RH2';
  case RAIN_OVERLAY:
    return 'RAINFALL';
  default:
    return '';
  }
}

function getUrlBase() {
  if (constants.ENV === 'weixin') {
    return '';
  }
  // return 'https://app-portal-ppe1.envisioniot.com';
  // 为了在cordova.js载入之前获得从native传过来的服务器地址，采用EnvContextPlugin 截获 html
  // 中发送到http://___/envcontext.js的请求，并返回执行一段js, 写入window.navigator.serverAddress
  // 注意EnvContextPlugin在config.xml中需要配置onload=true的参数，这样不用等到cordova.js调用相应的javascript
  // 函数，就可以初始化plugin
  const serverAddress = window.navigator.serverAddress;
  if (!serverAddress && device.isShell) {
    throw new Error('window.navigator.serverAddress 没有赋值！');
  }
  // 当从cordova app内，以file:// 协议启动的时候，直接连服务器后台，否则通过本地服务器做反向代理
  const urlBase = device.isShell ? serverAddress : `${window.location.protocol}//${window.location.host}`;
  return urlBase;
}

const dao = {
  getWindSpeed() {
    // todo 这里的masterId是临时加了一个风机的后缀WTG0001
    const now = Date.now();
    const paramTime = weatherStore.requestDataTime || now - (now % 3600000);
    return fetchData('/kongming/cfd/api/data/sourceType/GFS/region/GLOBAL/attribute/WS_UV', {time: paramTime}, {type: 'GET'})
      .then(data => weatherStore.setOverlayData(data))
      .catch((data) => {
        console.error(data);
      });
  },

  getSlyWindSpeed() {
    // todo 这里的masterId是临时加了一个风机的后缀WTG0001
    const now = Date.now();
    return fetchData('/kongming/cfd/api/data/sourceType/WRF_SARAWAK/region/SARAWAK/attribute/WS_UV', {time: now - (now % 3600000)}, {type: 'GET'})
      .then(data => weatherStore.setOverlayData(data))
      .catch((data) => {
        console.error(data);
      });
  },
  getDataAction(overlay, paramTime) {
    // 风图层请求数据
    if (overlay === WIND_OVERLAY) {
      const UDataPromise = fetchData(`${getUrlBase()}/enweather/api/surface/data`, {time: paramTime, weatherelement: getParamsUrl(overlay)[0], mode: constants.mode}, {type: 'GET'});
      const VDataPromise = fetchData(`${getUrlBase()}/enweather/api/surface/data`, {time: paramTime, weatherelement: getParamsUrl(overlay)[1], mode: constants.mode}, {type: 'GET'});
      return Promise.all([UDataPromise, VDataPromise]).then(data => weatherStore.setOverlayData({
        overlayName: overlay,
        requestTime: paramTime,
        data
      })
      )
        .catch((data) => {
          console.error(data);
        });
    }
    // 非风图层请求数据
    return fetchData(`${getUrlBase()}/enweather/api/surface/data`, {time: paramTime, weatherelement: getParamsUrl(overlay), mode: constants.mode}, {type: 'GET'})
      .then(data => weatherStore.setOverlayData({
        overlayName: overlay,
        requestTime: paramTime,
        data
      })
      )
      .catch((data) => {
        console.error(data);
      });
  },

  getSlyData(overlay) {
    // todo 这里的masterId是临时加了一个风机的后缀WTG0001
    const now = Date.now();
    const paramTime = weatherStore.requestDataTime || now - (now % 3600000);
    // const overlayDataCache = weatherStore.overlayData.get(`${overlay}-${paramTime}`);
    const overlayDataCache = weatherStore.overlayData.get(`${overlay}`);
    if (overlayDataCache) {
      if (overlayDataCache.requestTime === paramTime) {
        return new Promise((resolve) => {
          resolve(overlayDataCache.data);
        });
      }
    }
    // if (!constants.hasToken) {
    //   return constants.setTokenFun().then(() => dao.getDataAction(overlay, paramTime));
    // }
    return dao.getDataAction(overlay, paramTime);
  },
  getSlyDatas(overlay) {
    // todo 这里的masterId是临时加了一个风机的后缀WTG0001
    const now = Date.now();
    const paramTime = weatherStore.requestDataTime || now - (now % 3600000);
    // const overlayDataCache = weatherStore.overlayData.get(`${overlay}-${paramTime}`);
    const overlayDataCache = weatherStore.overlayData.get(`${overlay}`);
    if (overlayDataCache) {
      if (overlayDataCache.requestTime === paramTime) {
        return new Promise((resolve) => {
          resolve(overlayDataCache.data);
        });
      }
    }
    // 风图层请求数据
    if (overlay === WIND_OVERLAY) {
      const UDataPromise = fetchData('/enweather/api/surface/data', {time: paramTime, weatherelement: getParamsUrl(overlay)[0], mode: constants.mode}, {type: 'GET'});
      const VDataPromise = fetchData('/enweather/api/surface/data', {time: paramTime, weatherelement: getParamsUrl(overlay)[1], mode: constants.mode}, {type: 'GET'});
      return Promise.all([UDataPromise, VDataPromise]).then(data => weatherStore.setOverlayData({
        overlayName: overlay,
        requestTime: paramTime,
        data
      })
      )
        .catch((data) => {
          console.error(data);
        });
    }
    // 非风图层请求数据
    return fetchData('/enweather/api/surface/data', {time: paramTime, weatherelement: getParamsUrl(overlay), mode: constants.mode}, {type: 'GET'})
      .then(data => weatherStore.setOverlayData({
        overlayName: overlay,
        requestTime: paramTime,
        data
      })
      )
      .catch((data) => {
        console.error(data);
      });
  }
};

export default dao;

