/**
 * 20190731 by zuoyong.tang
 * react-leaflet的粒子流overlay插件
 * 支持输入气象数据、透明度、粒子流动频率
 * 实现：
 *  1、根据图像大小随机生成若干个点
 *  2、求每个点的2维风速
 *  3、根据风速求的下一帧这个风速点的位置
 *  4、执行一帧，把每个点移动到下一个位置
 *  5、反复执行以上2-4步
 */

/* eslint-disable no-multi-assign */
/* eslint-disable no-underscore-dangle */

import {withLeaflet} from 'react-leaflet';
import PropTypes from 'prop-types';
import {toJS} from 'mobx';
import WeatherMapLayer from '../WeatherMapLayer';
import {interpolate, formatVectorNew, formatCoordinates} from '../Utils';
import {WIND_OVERLAY} from '../../../pages/Weather/constants';

const PARTICLE_MULTIPLIER = 3.5; // particle count scalar (completely arbitrary--this values looks nice)
const FADE_FILL_STYLE = 'rgba(0, 0, 0, 0.85)'; // 越透明，粒子越短
const MAX_PARTICLE_AGE = 100; // max number of frames a particle is drawn before regeneration
const PARTICLE_LINE_WIDTH = 1.0; // line width of a drawn particle
const PARTICLE_STEP_RATE = 0.05; // 每帧粒子前进的幅度
const NULL_WIND_VECTOR = [NaN, NaN, null]; // singleton for undefined location outside the vector field [u, v, mag]

function getRandomArbitrary(min, max) {
  return Math.random() * (max - min) + min;
}

export default withLeaflet(
  class Particle extends WeatherMapLayer {
    static propTypes = {
      opacity: PropTypes.number,
      particleInterval: PropTypes.number
    };

    static defaultProps = {
      opacity: 0.6,
      particleInterval: 50
    };

    heatMapHandle() {
      console.log('粒子流');
    }

    createLeafletElement() {
      return null;
    }

    componentWillReceiveProps(nProps) {
      const cProps = this.props;

      if (nProps.particleData !== cProps.particleData || nProps.opacity !== cProps.opacity || nProps.particleInterval !== cProps.particleInterval) {
        this.reset();
      }
    }

    setOverlayInfoHandle(ev) {
      const {currentInfo} = this.props;
      const latLng = ev.latlng;
      const coordText = formatCoordinates(latLng.lng, latLng.lat);
      let infoList;
      if (this.latLngValid(latLng)) {
        const windValue = interpolate(latLng.lng, latLng.lat, this._grid, this._header, WIND_OVERLAY);
        const formatOverlayInfo = {
          windDirectionAngle: formatVectorNew(windValue).windDirectionAngle,
          windSpeedNum: formatVectorNew(windValue).windSpeedNum
        };
        infoList = {
          containerPoint: ev.containerPoint,
          formatOverlayInfo: Object.assign(currentInfo.formatOverlayInfo, formatOverlayInfo),
          coordinates: {
            coordText,
            latLng
          }
        };
      } else {
        infoList = {
          containerPoint: ev.containerPoint,
          formatOverlayInfo: {},
          coordinates: {
            coordText,
            latLng
          }
        };
      }
      this.props.setOverlayInfoFromParent(infoList);
    }

    updateCurrentInfo() {
      const {currentInfo} = this.props.weatherStore; // 此处直接获取weatherStore的值。尝试从props中间接获取currentInfo,获取到的是上一次的值。
      const latLng = toJS(currentInfo.coordinates.latLng);
      let infoList;
      let formatOverlayInfo;
      const point = latLng.lat ? this.props.leaflet.map.latLngToContainerPoint(latLng) : {x: 0, y: 0};
      if (this.latLngValid(latLng)) {
        const windValue = interpolate(latLng.lng, latLng.lat, this._grid, this._header, WIND_OVERLAY);
        formatOverlayInfo = {
          windDirectionAngle: formatVectorNew(windValue).windDirectionAngle,
          windSpeedNum: formatVectorNew(windValue).windSpeedNum
        };
        infoList = {
          coordinates: {
            coordText: formatCoordinates(latLng.lng, latLng.lat),
            latLng
          },
          formatOverlayInfo: Object.assign(currentInfo.formatOverlayInfo, formatOverlayInfo),
          containerPoint: point
        };
      } else {
        formatOverlayInfo = {
          windDirectionAngle: null,
          windSpeedNum: null
        };
        infoList = {
          coordinates: {
            coordText: formatCoordinates(latLng.lng, latLng.lat),
            latLng
          },
          formatOverlayInfo: Object.assign(currentInfo.formatOverlayInfo, formatOverlayInfo),
          containerPoint: point
        };
      }
      this.props.setOverlayInfoFromParent(Object.assign(currentInfo, infoList));
    }

    redraw() {
      this.parseData(this.props.particleData, this.props.header);

      if (this._data) {
        this.animate(this.createField(this.getBounds(), this.props.leaflet.map));
        this.updateCurrentInfo();
        this._frame = null;
      }
    }

    animate(field) {
      if (this._animateTimer) {
        clearTimeout(this._animateTimer);
      }
      const bounds = this.getBounds();
      const particleCount = Math.round((bounds.height + bounds.width) * PARTICLE_MULTIPLIER);

      const particles = [];
      for (let i = 0; i < particleCount; i++) {
        particles.push(field.randomize({age: Math.random() * MAX_PARTICLE_AGE}));
      }

      const size = this.props.leaflet.map.getSize();
      const context = this._el.getContext('2d');
      context.clearRect(0, 0, size.x, size.y);
      context.lineWidth = PARTICLE_LINE_WIDTH;
      context.fillStyle = FADE_FILL_STYLE;

      const frame = () => {
        try {
          // console.time('particles calc');
          this.evolve(field, particles);
          // console.timeEnd('particles calc');
          // console.time('particles animate');
          this.draw(context, particles, bounds);
          // console.timeEnd('particles animate');
          this._animateTimer = setTimeout(frame, this.props.particleInterval);
        } catch (e) {
          console.error(e);
        }
      };
      frame();
    }

    // 求每个点的下一个位置
    evolve(field, particles) {
      particles.forEach((particle) => {
        if (particle.age > MAX_PARTICLE_AGE) {
          field.randomize(particle).age = 0;
        }
        const x = particle.x;
        const y = particle.y;
        // console.time('field');
        const v = field(x, y); // vector at current position
        // console.timeEnd('field');
        const m = v[2];
        if (m === null) {
          particle.age = MAX_PARTICLE_AGE; // particle has escaped the grid, never to return...
        } else {
          const xt = x + v[0];
          const yt = y + v[1];
          if (field.isDefined(xt, yt)) {
            // Path from (x,y) to (xt,yt) is visible, so add this particle to the appropriate draw bucket.
            particle.xt = xt;
            particle.yt = yt;
          } else {
            // Particle isn't visible, but it still moves through the field.
            particle.x = xt;
            particle.y = yt;
          }
        }
        particle.age += 1;
      });
    }

    // canvas对每个点移动到下一个位置
    draw(context, particles, bounds) {
      // Fade existing particle trails.
      const prev = context.globalCompositeOperation;
      context.globalCompositeOperation = 'destination-in';
      context.fillRect(bounds.xMin, bounds.yMin, bounds.width, bounds.height);
      context.globalCompositeOperation = prev;

      context.beginPath();
      context.strokeStyle = `rgba(255, 255, 255, ${this.props.opacity})`;
      particles.forEach((particle) => {
        // 100是为了防止飞线
        // todo，有飞线的原因是什么？
        if (particle.x >= bounds.xMin && particle.x <= bounds.xMax && Math.abs(particle.xt - particle.x) < 100 && Math.abs(particle.yt - particle.y) < 100) {
          context.moveTo(particle.x, particle.y);
          context.lineTo(particle.xt, particle.yt);
        }
        particle.x = particle.xt;
        particle.y = particle.yt;
      });
      context.stroke();
    }

    // 根据当前两个维度的风速和缩放比例获取两个维度的粒子长度
    getVector(value, bounds, zoom) {
      return [PARTICLE_STEP_RATE * value[0] * zoom, 0 - PARTICLE_STEP_RATE * value[1] * zoom];
    }

    createField(bounds, leafletMap) {
      const _self = this;
      const mapSize = leafletMap.getSize();
      const dpr = 2; // todo，参考earth，这里的dpr是1也不会卡
      const nx = Math.ceil(mapSize.x / dpr);

      /**
       * @returns {Array} wind vector [u, v, magnitude] at the point (x, y), or [NaN, NaN, null] if wind
       *          is undefined at that point.
       */
      function field(xa, ya) {
        let x = Math.round(xa);
        let y = Math.round(ya);
        x -= x % dpr; // 3个像素，为了提升性能
        y -= y % dpr; // todo 性能需要进一步优化
        const index = (y / dpr) * nx + x / dpr;
        let vector = field.cache[index];
        if (vector) return vector;
        const latLng = _self.getLatLngFromCache(x, y, leafletMap);
        const value = interpolate(latLng.lng, latLng.lat, _self._grid, _self._header);
        vector = value ? [..._self.getVector(value, bounds, _self._zoom), value[2]] : NULL_WIND_VECTOR;
        field.cache[index] = vector;
        return vector;
      }

      field.cache = []; // 缓存像像素点的二维风速

      /**
       * @returns {boolean} true if the field is valid at the point (x, y)
       */
      field.isDefined = function(x, y) {
        return field(x, y)[2] !== null;
      };
      field.isDefined = () => true; // 为了提升性能，不再判断是否有效

      /**
       * @returns {boolean} true if the point (x, y) lies inside the outer boundary of the vector field, even if
       *          the vector field has a hole (is undefined) at that point, such as at an island in a field of
       *          ocean currents.
       */
      field.isInsideBoundary = function(x, y) {
        return field(x, y) !== NULL_WIND_VECTOR;
      };

      field.randomize = function(o) {
        // UNDONE: this method is terrible
        let x;
        let y;
        let safetyNet = 0;
        do {
          x = Math.round(getRandomArbitrary(bounds.xMin, bounds.xMax));
          y = Math.round(getRandomArbitrary(bounds.yMin, bounds.yMax));
        } while (!field.isDefined(x, y) && safetyNet++ < 30);
        o.x = x;
        o.y = y;
        return o;
      };

      return field;
    }
  }
);
