export default function weatherClass(str) {
  const weatherIcon = {
    晴: 'weather_sunny',
    少云: 'weather_cloudy',
    晴间多云: 'weather_partly_cloudy',
    多云: 'weather_cloudy',
    阴: 'weather_cloudy',
    阵雨: 'weather_moderate_rain',
    雷阵雨: 'weather_thunder_showery',
    雷阵雨并伴有冰雹: 'weather_thunder_showery',
    小雨: 'weather_light_rain',
    中雨: 'weather_moderate_rain',
    大雨: 'weather_heavy_rain',
    暴雨: 'weather_heavy_rain',
    大暴雨: 'weather_heavy_rain',
    特大暴雨: 'weather_heavy_rain',
    强阵雨: 'weather_heavy_rain',
    强雷阵雨: 'weather_thunder_showery',
    极端降雨: 'weather_heavy_rain',
    '毛毛雨/细雨': 'weather_light_rain',
    雨: 'weather_light_rain',
    '小雨-中雨': 'weather_light_rain',
    '中雨-大雨': 'weather_moderate_rain',
    '大雨-暴雨': 'weather_heavy_rain',
    '暴雨-大暴雨': 'weather_heavy_rain',
    '大暴雨-特大暴雨': 'weather_heavy_rain',
    雨雪天气: 'weather_sleet',
    雨夹雪: 'weather_sleet',
    阵雨夹雪: 'weather_sleet',
    冻雨: 'weather_sleet',
    雪: 'weather_moderate_snow',
    阵雪: 'weather_moderate_snow',
    小雪: 'weather_light_snow',
    中雪: 'weather_moderate_snow',
    大雪: 'weather_heavy_snow',
    暴雪: 'weather_heavy_snow',
    '小雪-中雪': 'weather_light_snow',
    '中雪-大雪': 'weather_moderate_snow',
    '大雪-暴雪': 'weather_heavy_snow',
    default: 'weather_cloudy'
  };
  return weatherIcon[str] || weatherIcon.default;
}
