  _chooseDate(num) {
    // let echarts_instance = this.echarts_react.getEchartsInstance();
    // echarts_instance.dispatchAction({
    //     type: 'hideTip'
    // });
    this.setState({
      currentDateIndex: num
    });
    if (num) {
      // 1为三天
      this.setState(preState => ({
        windCurData: preState.windData.slice(0, LONG_TIME),
        windCurTime: preState.timeList.slice(0, LONG_TIME)
      }), () => {
        this.setState(() => ({
          windInfoOption: this.getOption(this.props.weatherStore.currentOverlayName)
        }));
      });
    } else {
      // 0为日
      this.setState(preState => ({
        windCurData: preState.windData.slice(0, DAY),
        windCurTime: preState.timeList.slice(0, DAY)
      }), () => {
        this.setState(() => ({
          windInfoOption: this.getOption(this.props.weatherStore.currentOverlayName)
        }));
      });
    }
    // option.dataZoom[0].end =  === 100 ? 99.99 : 100;
  }

  _restoreRender() {
    const option = this.getOption(this.props.weatherStore.currentOverlayName);
    option.dataZoom[0].start = 0;
    option.dataZoom[0].end = this.state.windInfoOption.dataZoom[0].end === 100 ? 99.99 : 100;
    this.setState({
      windInfoOption: option,
      isDataZoom: false
    });
  }

  onDataZoom = () => {
    if (!this.state.isDataZoom) {
      this.setState({
        isDataZoom: true
      });
    }
  }

