/* eslint-disable max-len */

/**
 * 资源文件的JS对象是两级嵌套
 * 每个key大写字母表示，用下划线作为分隔
 * key值尽量加个前缀：
 *
TITLE  自适应，不建议超过30  标题文本
BTN  自适应，不建议超过20  按钮文本
COLH  自适应，不建议超过20  表格列头
INLINE  不超过300  说明文本，附属文本等
TXT  无  普通文本
HOVER  不建议超过100  Tooltip中显示的文本
LABEL  不建议超过30  标签，比如登录框上的“用户名”“密码”文本
HINT  不建议超过50  Placeholder, 输入框等位置的占位符
WINMSG  无  页面顶部弹出的提示信息
FORMMSG  不建议超过50  表单上的提示信息，比如“输入格式不正确”
 */

export default {
  LOGIN: {
    LABEL_LOGOUT: '登出',
    BTN_LOGIN: '登录',
    LABEL_USER: '用户名',
    LABEL_PASSWORD: '密码'
  },
  FC_DETAIL: {
    TEST: 'Test Demo Page'
  }
};
