/**
 * 20190731 by zuoyong.tang
 * react-leaflet的热力图overlay插件
 * 支持输入气象数据、透明度、颜色梯度表、马赛克大小等
 * 实现：
 *  逐个像素遍历
 *  求每个像素的经纬度
 *  根据经纬度求热力值
 *  根据热力值求颜色
 */

/* eslint-disable no-multi-assign */
/* eslint-disable no-underscore-dangle */
import {withLeaflet} from 'react-leaflet';
import PropTypes from 'prop-types';
// import t from './t.json';
import {toJS} from 'mobx';
import constants from 'common/utils/constants';
import WeatherMapLayer from '../WeatherMapLayer';
import {interpolate, extendedSinebowColor, segmentedColorScale, formatCoordinates} from '../Utils';
import {WIND_OVERLAY} from '../../../pages/Weather/constants';

export default withLeaflet(
  class LinearHeatmapLayer extends WeatherMapLayer {
    static propTypes = {
      gradient: PropTypes.object, // 颜色梯度，示例：{'0': 'rgb(255, 255, 255)', '0.5': 'rgb(0, 255, 255)', '1': 'rgb(255, 0, 255)'}
      dpr: PropTypes.number, // 每个颜色块的像素尺寸，越大马赛克越大，页面性能越好
      opacity: PropTypes.number
    };

    static defaultProps = {
      dpr: 2,
      valueBound: [0, 1],
      opacity: 0.2
    };

    constructor(props) {
      super(props);
      this.state = {
        currentInfo: {
          coordText: '',
          formatOverlayInfo: {}
        }
      };
      this._gradient = null;
      this._gradientFn = extendedSinebowColor;
    }

    // 自定义颜色阶梯
    parseGradient(gradient) {
      if (!gradient) {
        this._gradient = gradient;
        this._gradientFn = extendedSinebowColor;
      } else if (this._gradient !== gradient) {
        this._gradient = gradient;
        this._gradientFn = segmentedColorScale(
          Object.keys(gradient)
            .sort((a, b) => Number(a) - Number(b))
            .map(k => [
              Number(k),
              gradient[k]
                .split(/[(),]/)
                .slice(1, 4)
                .map(Number)
            ])
        );
      }
    }

    redraw() {
      this.parseData(this.props.heatData, this.props.header);
      this.parseGradient(this.props.gradient);

      if (this._data) {
        const canvas = this._el;
        const context = canvas.getContext('2d');
        const imageData = context.createImageData(canvas.width, canvas.height);
        const data = imageData.data;

        const leafletMap = this.props.leaflet.map;
        const size = leafletMap.getSize();
        const {dpr, opacity, currentOverlayName, rainOpacityNum, rainOpacityGlobalNum} = this.props;

        // const first = this.props.gradient[0];
        // const firstColor = first
        //   .substr(4, first.length - 5)
        //   .split(',')
        //   .map(item => parseInt(item))
        //   .concat(opacity * 255);
        for (let y = 0; y < size.y; y += dpr) {
          for (let x = 0; x < size.x; x += dpr) {
            const latLng = this.getLatLngFromCache(x, y, leafletMap);
            if (this.latLngValid(latLng)) {
              const value = interpolate(latLng.lng, latLng.lat, this._grid, this._header, currentOverlayName); // 有一定概率导致算出来为null，因为经纬度有效时，取整仍然可能取到有效的经纬度外
              /* eslint-disable */
              // const color = value === 0 ? firstColor : value === null ? [0, 0, 0, 0] : this._gradientFn(value && this.getValuePosition(value[2] || value), opacity * 255);
              /* eslint-disable */
              let color;
              let v = value === null ? 0 : value[2];
              if (v === undefined) {
                v = value;
              }
              if (constants.mode === constants.modeList[2]) {
                if (currentOverlayName === 'rain' && value < rainOpacityGlobalNum) {
                  color = [0, 0, 0, 0];
                } else {
                  color = value === null ? [0, 0, 0, 0] : this._gradientFn(this.getValuePosition(v), opacity * 255);
                }
              } else {
                if (currentOverlayName !== 'rain') {
                  color = value === null ? [0, 0, 0, 0] : this._gradientFn(this.getValuePosition(v), opacity * 255);
                } else if (value > rainOpacityNum) {
                  color = value === null ? [0, 0, 0, 0] : this._gradientFn(this.getValuePosition(v), opacity * 255);
                } else {
                  // color = value === null ? [0, 0, 0, 0] : this._gradientFn(this.getValuePosition(value[2] || value), opacity * 255);
                  color = [0, 0, 0, 0];
                }
              }
              // const color = value === null ? [0, 0, 0, 0] : this._gradientFn(this.getValuePosition(value[2] || value), opacity * 255);
              this.fillPoint(data, x, y, color, size, dpr);
            }
          }
        }

        // for (let i = 0; i < 1000000; i++) {
        //   data[i] = t[i];
        // }
        context.putImageData(imageData, 0, 0);
        this.updateCurrentInfo();
        this._frame = null;
      }
    }

    getValuePosition(v) {
      const {valueBound} = this.props;
      if (this.props.currentOverlayName === 'rain') {
        return v;
      }
      return (v - valueBound[0]) / (valueBound[1] - valueBound[0]);
    }

    // 如果分辨率是2，则一次填充4个像素，每个像素的颜色一致
    fillPoint(data, x, y, color, size, dpr = 1) {
      for (let i = x; i < x + dpr; i++) {
        for (let j = y; j < y + dpr; j++) {
          const p = 4 * (j * size.x + i);
          data[p] = color[0];
          data[p + 1] = color[1];
          data[p + 2] = color[2];
          data[p + 3] = color[3];
        }
      }
    }

    updateCurrentInfo() {
      const {currentInfo, currentOverlayName} = this.props.weatherStore;
      const latLng = toJS(currentInfo.coordinates.latLng);
      let infoList;
      let formatOverlayInfo;
      const point = latLng.lat ? this.props.leaflet.map.latLngToContainerPoint(latLng) : {x: 0, y: 0};
      if (currentOverlayName === WIND_OVERLAY) return;
      if (this.latLngValid(latLng)) {
        const heatData = interpolate(latLng.lng, latLng.lat, this._grid, this._header, this.props.currentOverlayName);
        formatOverlayInfo = {
          heatNum: heatData
        };
        infoList = {
          coordinates: {
            coordText: formatCoordinates(latLng.lng, latLng.lat),
            latLng
          },
          formatOverlayInfo: Object.assign(currentInfo.formatOverlayInfo, formatOverlayInfo),
          containerPoint: point
        };
      } else {
        formatOverlayInfo = {
          heatNum: null
        };
        infoList = {
          coordinates: {
            coordText: formatCoordinates(latLng.lng, latLng.lat),
            latLng
          },
          formatOverlayInfo: Object.assign(currentInfo.formatOverlayInfo, formatOverlayInfo),
          containerPoint: point
        };
      }
      this.props.setOverlayInfoFromParent(Object.assign(currentInfo, infoList));
    }

    setOverlayInfoHandle(ev) {
      const {currentInfo, currentOverlayName} = this.props;
      this.props.setFromFarmStatus(false);
      const latLng = ev.latlng;
      const coordText = formatCoordinates(latLng.lng, latLng.lat);
      let infoList;
      if (currentOverlayName === WIND_OVERLAY) return;
      if (this.latLngValid(latLng)) {
        const heatData = interpolate(latLng.lng, latLng.lat, this._grid, this._header, this.props.currentOverlayName);
        const formatOverlayInfo = {
          heatNum: heatData
        };
        infoList = {
          containerPoint: ev.containerPoint,
          formatOverlayInfo: Object.assign(currentInfo.formatOverlayInfo, formatOverlayInfo),
          coordinates: {
            coordText,
            latLng
          }
        };
      } else {
        infoList = {
          containerPoint: ev.containerPoint,
          formatOverlayInfo: {},
          coordinates: {
            coordText,
            latLng
          }
        };
      }
      this.props.setOverlayInfoFromParent(infoList);
    }
  }
);
