/**
 * 20190510 by zuoyong.tang
 * 使用dpl的工具类实现国际化
 * 使用方法：
 * 1、在/common/locales中编写各个语言的资源文件
 * 2、在页面中引入：import {pageMsg} from 'common/Locale';
 * 3、在页面中定义变量：const loginMsg = pageMsg('LOGIN');
 * 4、调用 loginMsg('BTN_LOGIN') 则返回当前语言的文本，比如‘登录’/‘Login’
*/

import Locale from '@enos/dpl/lib/locale';
import dplen from '@enos/dpl/lib/locale/lang/en_US';
import dplzh from '@enos/dpl/lib/locale/lang/zh_CN';
import {setCookie, getCookie} from './Utils.js';
import localeText, {initUI, initMessage, getLocale, plugin} from './LocaleUtils.js';

function loadAllLocaleResources() {
  let req = require.context('./locales', false, /\.js$/);
  let resources = {};

  req.keys().forEach((file) => {
    const locale = file.replace('./', '').replace('.js', '');
    resources[locale] = req(file).default;
  });

  initUI(resources);

  req = require.context('./locales/messages', false, /\.js$/);
  resources = {};

  req.keys().forEach((file) => {
    const locale = file.replace('./', '').replace('.js', '');
    resources[locale] = req(file).default;
  });

  initMessage(resources);
}

loadAllLocaleResources();
const selectedLocale = getLocale();
setCookie('locale', selectedLocale);
setCookie('lang', selectedLocale);

export function isI18nDebug() {
  return getCookie('i18nDebug') === 'enabled';
}

function loadLocalStorageResource() {
  if (isI18nDebug()) {
    const returnValue = {};
    ['i18nUIs', 'i18nMessages'].forEach((storageKey) => {
      let localeStorageI18n = localStorage.getItem(storageKey);
      if (localeStorageI18n) {
        try {
          localeStorageI18n = JSON.parse(localeStorageI18n);
          returnValue[storageKey] = {[selectedLocale]: localeStorageI18n};
        } catch (error) {
          console.error(error);
        }
      }
    });
    plugin(() => returnValue);
  }
}
loadLocalStorageResource();

export default function msg(key, ...valueArr) {
  const value = /* localeTextFromDebug(key, ...valueArr) || */ localeText(key, ...valueArr);

  if (!value) {
    return key;
  }

  return value;
}

export function pageMsg(pageKey) {
  return (key, ...valueArr) => msg(`${pageKey}.${key}`, ...valueArr);
}

export function enableDplLang() {
  if (Locale && Locale.use) {
    if (selectedLocale && selectedLocale.toLowerCase().indexOf('en') > -1) {
      Locale.use(dplen);
    } else {
      Locale.use(dplzh);
    }
  }
}

// 默认初始化dpl的国际化内容
enableDplLang();

msg.locale = selectedLocale;
msg.lang = selectedLocale.replace(/[-_].*$/, '');
