import React, {Component} from 'react';
import {CSSTransition} from 'react-transition-group';
import {observer} from 'mobx-react';
import i18n from 'common/i18n';
import weatherStore from '../../pages/Weather/models/WeatherStore.js';
import './index.css';

@observer
class OverlayControl extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showSourceArea: false
    };
    this.showSourceHandle = this.showSourceHandle.bind(this);
    this.getAllSourceHandle = this.getAllSourceHandle.bind(this);
    this.setSourceHandle = this.setSourceHandle.bind(this);
    this.setOverayHandle = this.setOverayHandle.bind(this);
    this.hideOperationHandle = this.hideOperationHandle.bind(this);
    this.changeParticleStatusHandle = this.changeParticleStatusHandle.bind(this);
    this.changeMapModeHandle = this.changeMapModeHandle.bind(this);
  }

  showSourceHandle() {
    this.setState(preState => ({
      showSourceArea: !preState.showSourceArea
    }));
  }

  getAllSourceHandle() {
    return this.props.weatherStore.sourceList.map(item => (
      <span key={item} onClick={() => this.setSourceHandle(item)} className='sourceItem'>
        {item}
      </span>
    ));
  }

  setSourceHandle(source) {
    this.props.changeSourceFromParent(source);
    this.setState(() => ({
      showSourceArea: false
    }));
  }

  getAllOverlayHandle() {
    return this.props.weatherStore.overlayList.map(overlay => (
      <div
        key={overlay.name}
        onClick={() => {
          this.setOverayHandle(overlay.name);
        }}
        className={`overlayItem ${this.props.weatherStore.currentOverlayName === overlay.name ? 'focus' : ''}`}
      >
        <div className='overlayCont'>
          <div className='radioBox'>
            <span className={`overlayIcon ${overlay.name}Icon`} />
          </div>
          <span className='overlayName'>{overlay.i18nName}</span>
        </div>
      </div>
    ));
  }

  setOverayHandle(overlay) {
    this.props.changeOverlayFromParent(overlay);
  }

  hideOperationHandle() {
    this.props.weatherStore.setShowOperation(false);
  }

  changeParticleStatusHandle() {
    this.props.changeParticleAnimationStatusFromParent(!this.props.weatherStore.particleAnimationOpen);
  }

  changeMapModeHandle(modeNum) {
    this.props.weatherStore.setCurrentMapMode(modeNum);
  }

  render() {
    // const {showSourceArea} = this.state;
    const {showOperation, particleAnimationOpen, currentMapMode} = this.props.weatherStore;
    return (
      <CSSTransition
        in={showOperation} // 告诉组件根据show的值进行切换。
        timeout={300} // 动画执行多久
        className='fade overlayControlArea' // 此处的名称与第3步的fade需要保持一致。
      >
        <div>
          <div className='mask' onClick={this.hideOperationHandle} />
          <div className='contentBox'>
            {/* 数据源切换区域
            <div className='sourceArea'>
              <div className='curentSource'>
                <p className='sourceLabel'>
                  <span>{i18n.DATA_SOURCE}</span>
                  <span className='sourceText'>{this.props.weatherStore.currentSourceName}</span>
                </p>
                <div className='arrowBox' onClick={this.showSourceHandle}>
                  <span className='arrow_down' />
                </div>
              </div>
              <div className='optionArea' style={{display: `${showSourceArea ? 'block' : 'none'}`}}>
                {this.getAllSourceHandle()}
              </div>
            </div>
             */}
            {/* 地图模式切换 */}
            <div className='mapModeArea'>
              <p className='title'>{i18n.MAP}</p>
              <div className='mapModeBox'>
                <div className={`${currentMapMode === 0 ? 'item active' : 'item'}`} onClick={() => { this.changeMapModeHandle(0); }}>
                  <img src={require('common/img/overlay/mode/modeMap1.png')} alt={i18n.STANDARD_MAP} className='modeImg' />
                  <span className='modeText'>{i18n.STANDARD_MAP}</span>
                </div>
                <div className={`${currentMapMode === 1 ? 'item active' : 'item'}`} onClick={() => { this.changeMapModeHandle(1); }}>
                  <img src={require('common/img/overlay/mode/modeMap2.png')} alt={i18n.STANDARD_MAP} className='modeImg' />
                  <span className='modeText'>{i18n.SATELLITE_MAP}</span>
                </div>
              </div>
            </div>
            {/* 图层切换区域 */}
            <div className='overlayArea'>
              <p className='title'>{i18n.METEOROLOGICAL}</p>
              <div className='overlayBox'>{this.getAllOverlayHandle()}</div>
            </div>
            {/* 粒子动画开关 */}
            <div className='switchArea'>
              <input className='switch' type='checkbox' checked={particleAnimationOpen} onChange={this.changeParticleStatusHandle} />
              <span className='switchText'>{i18n.PARTICLE_ANIMATION}</span>
            </div>
          </div>
        </div>
      </CSSTransition>
    );
  }
}

export default props => <OverlayControl {...props} weatherStore={weatherStore} />;
