import fetch from 'isomorphic-fetch';
import qs from 'qs';
import {GLOBAL_ID_KEY} from 'common/config/constants';
import numeral from 'numeral';
import constants from './utils/constants';

export function getAppId() {
  if (constants.ENV === 'weixin') {
    return constants.AppId;
  }
  return window.navigator.appId || 'd67a92c4-f41f-4c09-87f5-cd520b4876ca';
}

export function getAuthorization() {
  const demoToken = getQueryString('demo_token');
  if (demoToken) {
    return `Bearer ${demoToken}`;
  }
  if (constants.ENV === 'weixin') {
    return constants.Authorization;
  }
  return `Bearer ${window.navigator.accessToken}`;
  // return 'Bearer APP_PORTAL_S_RtkRGjN8YJJJtLHvpreND3mqvfLMeqr5';
}

// 默认需要显示loading图标
function fetchDataAction(ajaxObj, options) {
  const headers = {
    Accept: 'application/json',
    Authorization: getAuthorization()
  };

  headers['Content-Type'] = ajaxObj.contentType || 'application/json; charset=UTF-8';

  const fetchObj = {
    method: ajaxObj.type || 'POST',
    headers,
    credentials: 'same-origin',
    cache: 'no-cache'
  };
  if (ajaxObj.type.toUpperCase() !== 'GET') {
    fetchObj.body = ajaxObj.data; // 这里的body已经转成了字符串
  }

  let loadingClass = 'nothing';
  if (options.noLoading) {
    loadingClass = initLoading();
  }

  return new Promise((resolve, reject) => {
    fetch(ajaxObj.url, fetchObj)
      .then((response) => {
        closeLoading(loadingClass);
        if (response.status >= 200 && response.status < 400) {
          return returnContentByType(response);
        }
        throw (response);
      })
      .then((data) => {
        if ((`${data.code}` === '30000' || `${data.code}` === '401') && options.check30000 !== false) {
          window.top.location = `/master/login.html?backurl=${encodeURIComponent(window.top.location.href)}`;
        } else if (data.code && `${data.code}` !== '10000') {
          resolve(data);
        } else {
          resolve(data);
        }
      })
      .catch((error) => {
        closeLoading(loadingClass);
        console.error(error);
        reject(error);
      });
  });
}

export function MathMax(args) {
  const values = Array.isArray(args[0]) ? args[0] : [...args];
  return values.reduce((l, n) => Math.max(l, n), Number.MIN_SAFE_INTEGER);
}

export function MathMin(args) {
  const values = Array.isArray(args[0]) ? args[0] : [...args];
  return values.reduce((l, n) => Math.min(l, n), Number.MAX_SAFE_INTEGER);
}

export function isArray(value) {
  return Object.prototype.toString.call(value) === '[object Array]';
}

export function isKVObject(value) {
  return Object.prototype.toString.call(value) === '[object Object]';
}

export function isFunction(value) {
  return Object.prototype.toString.call(value) === '[object Function]';
}

export function isString(value) {
  return Object.prototype.toString.call(value) === '[object String]';
}

export function formatTime(date, fmt) {
  if (!fmt || fmt === 0 || fmt === null) {
    return formatTime(date, 'yyyy-MM-dd hh:mm:ss');
  }

  const o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 小时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    S: date.getMilliseconds() // 毫秒
  };
  // eslint-disable-next-line no-param-reassign
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (`${date.getFullYear()}`).substr(4 - RegExp.$1.length));
  // eslint-disable-next-line no-restricted-syntax
  for (const k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      // eslint-disable-next-line no-param-reassign
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : ((`00${o[k]}`).substr((`${o[k]}`).length)));
    }
  }
  return fmt;
}

export function toFixed(v, m) {
  let n = m;
  if (n !== undefined && (isNaN(n) || Number(n) > 17 || Number(n) < 0)) {
    throw new Error('输入正确的精度范围');
  }
  // 拆分小数点整数和小数
  const num = v;
  const numList = num.toString().split('.');
  // 整数
  let iN = Number(numList[0]);
  // 小数
  const dN = numList[1];
  n = parseInt(n);
  if (isNaN(n) || Number(n) === 0) {
    // 0或者不填的时候，按0来处理
    if (dN === undefined) {
      return `${num}`;
    }
    const idN = Number(dN.toString().substr(0, 1));
    if (idN >= 5) {
      iN += 1;
    }
    return iN;
  }
  const dNL = dN === undefined ? 0 : dN.length;
  if (dNL < n) {
    // 如果小数位不够的话，那就补全
    let oldN = `${num}.`;
    let a = Number(n) - dNL;
    while (a > 0) {
      oldN += '0';
      a--;
    }
    return oldN;
  }
  // 正常
  let dN1 = Number(dN.toString().substring(0, n));
  const dN2 = Number(dN.toString().substring(n, n + 1));
  if (dN2 >= 5) {
    dN1 += 1;
  }
  return `${iN}.${dN1}`;
}

export function checkLogin() {
  if (!getCookie(GLOBAL_ID_KEY)) {
    // window.location.hash = '#/login';
  }
}

export function fetchData(path, data, options = {}) {
  checkLogin();
  const {type, contentType} = options;
  let requestData = '';
  const requestType = type || 'POST';
  const ajaxObj = {
    url: path,
    type: requestType,
    data: requestData,
    contentType
  };

  if (data) {
    if (isString(data)) {
      // Most of time, this's used for requestbody
      requestData = data;
    } else if (requestType.toUpperCase() === 'GET') {
      const params = [];
      Object.keys(data).forEach((key) => {
        let val = data[key];
        if (isArray(val) || isKVObject(val)) {
          val = JSON.stringify(val);
        }
        params.push(`${key}=${encodeURIComponent(val)}`);
      });
      requestData = params.join('&');
    } else {
      console.error('Post请求的Payload需要是字符串');
    }

    if (requestType.toUpperCase() !== 'GET') {
      ajaxObj.data = requestData;
    } else {
      ajaxObj.url = `${path}${path.indexOf('?') >= 0 ? '&' : '?'}${requestData}`;
    }
  }

  return fetchDataAction(ajaxObj, options);
}


// 获取cookie
export function getCookie(name) {
  const start = document.cookie.indexOf(`${name}=`);
  const len = start + name.length + 1;
  if ((!start) && (name !== document.cookie.substring(0, name.length))) {
    return null;
  }
  if (start === -1) return null;
  let end = document.cookie.indexOf(';', len);
  if (end === -1) end = document.cookie.length;
  return unescape(document.cookie.substring(len, end));
}

// 设置cookie
export function setCookie(name, value, path = '/', days = 30) {
  const exp = new Date();
  exp.setTime(exp.getTime() + days * 24 * 60 * 60 * 1000);
  document.cookie = `${name}=${escape(value)};expires=${exp.toGMTString()}; path=${path}`;
}

// 设置cookie
export function setCookieDefalutExpires(name, value, path = '/') {
  document.cookie = `${name}=${escape(value)}; path=${path}`;
}

// 清除cookie
export function clearCookie(key, path = '/') {
  const keys = (key && [key]) || document.cookie.match(/[^ =;]+(?==)/g) || [];

  for (let i = keys.length; i--;) {
    document.cookie = `${keys[i]}=0;expires=${new Date(0).toUTCString()}; path=${path}`;
  }
}

export function emptyValueWrapper(v) {
  if (v === null || v === undefined || isNaN(v)) {
    return '--';
  }
  return v;
}

export function percentValueWrapper(v) {
  if (v === null || v === undefined || isNaN(v)) {
    return '--';
  }
  // 当值为100%时不保留小数位
  return `${toFixed(v, 2) === '100.00' ? toFixed(v, 0) : toFixed(v, 2)}%`;
}

export function numberValueWrapper(v) {
  if (v === null || v === undefined || isNaN(v)) {
    return '--';
  }
  return `${numeral(v).format('0, 0.00')}`;
}

function returnContentByType(response) {
  const type = (response.headers.get('Content-Type') || '').split(';')[0].toLowerCase();
  switch (type) {
  case 'text/html':
  case 'application/octet-stream':
  default:
    return response.text();
  case '':
    return response.json();
  case 'application/json':
    return response.json();
  }
}

function initLoading() {
  let div = document.querySelector('.EnOS-loading');
  if (!div) {
    div = document.createElement('div');
    div.className = 'EnOS-loading';
    document.body.appendChild(div);
  }
  let className = div.className || '';
  className = className.trim().split(/\s+/);
  const addClass = `loading-${className.length}-${new Date().getTime()}`;
  className.push(addClass);
  div.className = className.join(' ');
  return addClass;
}

function closeLoading(addClass) {
  const div = document.querySelector('.EnOS-loading');
  if (div) {
    setTimeout(() => {
      let className = div.className || '';
      if (addClass) {
        className = className.replace(addClass, '');
        div.className = className;
      } else {
        div.className = 'EnOS-loading';
      }
    }, 100);
  }
}

CanvasRenderingContext2D.prototype.sector = function(x, y, r, angle1, angle2) {
  this.save();
  this.beginPath();
  this.moveTo(x, y);
  this.arc(x, y, r, angle1 * Math.PI / 180, angle2 * Math.PI / 180, false);
  this.closePath();
  this.restore();
  return this;
};

// 改变某个对象中的属性。
export function editAttributeFn(path, value, obj) {
  const arr = path.split('.');
  const len = arr.length - 1;
  arr.reduce((cur, key, index) => {
    if (!(cur[key])) {
      throw new Error(`${key} 不存在!`);
    }
    if (index === len) {
      cur[key] = value;
    }
    return cur[key];
  }, obj);
}

export function verifylonglat(longitude, latitude) {
  // 经度，整数部分为0-180小数部分为0到6位
  const longreg = /^(-|\+)?(((\d|[1-9]\d|1[0-7]\d|0{1,3})\.\d{0,6})|(\d|[1-9]\d|1[0-7]\d|0{1,3})|180\.0{0,6}|180)$/;
  if (!longreg.test(longitude)) {
    console.log('经度整数部分为0-180,小数部分为0到6位!');
    return false;
  }
  // 纬度,整数部分为0-90小数部分为0到6位
  const latreg = /^(-|\+)?([0-8]?\d{1}\.\d{0,6}|90\.0{0,6}|[0-8]?\d{1}|90)$/;
  if (!latreg.test(latitude)) {
    console.log('纬度整数部分为0-90,小数部分为0到6位!');
    return false;
  }
  return true;
}

function getQueryString(name) {
  return qs.parse(window.location.search.slice(1))[name] || '';
}

export const language = (
  getQueryString('locale')
  || getQueryString('lang')
  || getQueryString('language')
  || navigator.language
  || (navigator && navigator.languages && navigator.languages[0])
  || navigator.userLanguage
  || 'zh-CN'
).substr(0, 2);
