import React, {Component, Fragment} from 'react';
import moment from 'moment';
import Slider, {createSliderWithTooltip} from 'rc-slider';
import i18n from 'common/i18n';
import TicksLabel from './TicksLabel';
import Tooltip from './Tooltip';
import PlayButton from './PlayButton';
import 'rc-slider/assets/index.css';
import './index.css';

const SliderWithTooltip = createSliderWithTooltip(Slider);

const autoPlayInterval = 2 * 1000; // 播放间隔时间
let autoPlayTimer = null;

const ticksLabelLen = 3; // 进度条天数
const getTicksLabel = () => {
  let todayMO = moment();
  const labelList = [];
  // const weekStrList = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];
  const weekStrList = [i18n.SUN, i18n.MON, i18n.TUE, i18n.WED, i18n.THU, i18n.FRI, i18n.SAT];
  for (let i = 0; i < ticksLabelLen; i++) {
    labelList[i] = {
      labelDate: `${todayMO.format('MM/DD')}`,
      labelWeek: `${weekStrList[parseInt(todayMO.format('d'))]}`
    };
    todayMO = todayMO.add(1, 'd');
  }
  return labelList;
};
const sliderWholeTime = ticksLabelLen * (24 * 60 * 60 * 1000); // 整个进度条的时间
class TimeSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentValue: 0,
      progressMaxValue: ticksLabelLen * 24,
      toolTipVal: '', // toolTip组件显示的文本
      sliderOffsetInfo: {},
      autoPlayStatus: false // 自动播放状态
    };
    this.percentFormatter = this.percentFormatter.bind(this);
    this.onChangeHandle = this.onChangeHandle.bind(this);
    this.onAfterChangeHandle = this.onAfterChangeHandle.bind(this);
    this.setRequestTimeHandle = this.setRequestTimeHandle.bind(this);
    this.sliderChangeHandle = this.sliderChangeHandle.bind(this);
    this.tipFormatter = this.tipFormatter.bind(this);
    this.initSliderTime = this.initSliderTime.bind(this);
    this.getSliderWidth = this.getSliderWidth.bind(this);
    this.setProgressHandle = this.setProgressHandle.bind(this);
    this.autoPlayHandle = this.autoPlayHandle.bind(this);
    this.setMouseMoveFromTooltip = this.setMouseMoveFromTooltip.bind(this);
    this.setMouseUpFromTooltip = this.setMouseUpFromTooltip.bind(this);
  }

  componentDidMount() {
    this.getSliderWidth();
    this.initSliderTime();
    this.isSliderChange = false;
    this.isTooltipChange = false;

    window.addEventListener('resize', this.getSliderWidth);
  }

  componentWillReceiveProps(nextPorps) {
    if (nextPorps.showInfoPanelStatus !== 0) {
      this.setState({
        autoPlayStatus: false
      }, () => {
        this.clearIntervalHandle();
      });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.getSliderWidth);
  }

  initSliderTime() {
    this.setState({
      toolTipVal: moment().set({minute: 0, second: 0, millisecond: 0}).format('HH:mm'),
      currentValue: parseInt(moment().format('H'))
    });
  }

  percentFormatter(v) {
    return `${v} %`;
  }


  tipFormatter(value) {
    console.log(value);
  }

  onChangeHandle(value) {
    this.isSliderChange = true;
    this.clearIntervalHandle();
    this.toolTip.disAdjuestHandle(value / this.state.progressMaxValue);
    this.sliderChangeHandle(value);
  }

  onAfterChangeHandle(value) {
    if (this.isSliderChange) {
      this.setRequestTimeHandle(value);
      this.isSliderChange = false;
    }
  }

  setMouseMoveFromTooltip() {
    this.isTooltipChange = true;
  }

  setMouseUpFromTooltip(percent) {
    if (this.isTooltipChange) {
      this.setRequestTimeHandle(Math.floor(percent * this.state.progressMaxValue));
      this.isTooltipChange = false;
    }
  }

  setRequestTimeHandle(value) {
    const {progressMaxValue} = this.state;
    const nowData = (value / progressMaxValue) * sliderWholeTime;
    const nowTime = moment().startOf('day').valueOf() + nowData;
    this.props.changeRequestDataTimeFromParent(nowTime);
  }

  sliderChangeHandle(value) {
    const {progressMaxValue} = this.state;
    const nowData = (value / progressMaxValue) * sliderWholeTime;
    const time = moment().startOf('day').valueOf() + nowData;
    const toolTipVal = moment(time).format('HH:mm');
    if (this.state.autoPlayStatus) {
      this.props.changeRequestDataTimeFromParent(time);
    }
    this.setState({
      currentValue: value,
      toolTipVal
    });
  }

  getSliderWidth() {
    const {width, height, left, top} = this.copySliderWidthEl.getBoundingClientRect();
    this.setState({
      sliderOffsetInfo: {width, height, left, top}
    });
  }

  setProgressHandle(percent) {
    this.isTooltipChange = true;
    const {progressMaxValue} = this.state;
    this.clearIntervalHandle();
    this.sliderChangeHandle(Math.floor(percent * progressMaxValue));
  }

  autoPlayHandle() {
    this.setState(preState => ({
      autoPlayStatus: !preState.autoPlayStatus
    }), () => {
      if (this.state.autoPlayStatus) {
        autoPlayTimer = setInterval(() => {
          const newVal = ++this.state.currentValue;
          this.sliderChangeHandle(newVal > this.state.progressMaxValue ? 0 : newVal);
        }, autoPlayInterval);
      } else {
        this.clearIntervalHandle();
      }
    });
  }

  clearIntervalHandle = () => {
    if (autoPlayTimer) {
      clearInterval(autoPlayTimer);
      this.setState({
        autoPlayStatus: false
      });
    }
  }

  render() {
    const {currentValue, progressMaxValue, toolTipVal, sliderOffsetInfo, autoPlayStatus} = this.state;
    return (
      <Fragment>
        <PlayButton
          autoPlayHandle={this.autoPlayHandle}
          autoPlayStatus={autoPlayStatus}
          playBtnRef={(ref) => { this.playBtn = ref; }}
        />
        <div className='sliderArea'>
          <TicksLabel ticksLabelLen={getTicksLabel()} />
          <Tooltip
            ref={(toolTip) => { this.toolTip = toolTip; }}
            playBtnComponent={this.playBtn}
            toolTipVal={toolTipVal}
            progressScale={currentValue / progressMaxValue}
            sliderOffset={sliderOffsetInfo}
            setProgressHandle={this.setProgressHandle}
            setRequestTimeHandle={this.setRequestTimeHandle}
            setMouseMoveFromTooltip={this.setMouseMoveFromTooltip}
            setMouseUpFromTooltip={this.setMouseUpFromTooltip}
          />
          <SliderWithTooltip
            className={currentValue === progressMaxValue ? 'fullStatus' : ''}
            min={0}
            max={progressMaxValue}
            value={currentValue}
            tipFormatter={this.percentFormatter}
            onChange={this.onChangeHandle}
            onAfterChange={this.onAfterChangeHandle}
          />
          <div ref={(copySliderWidthEl) => { this.copySliderWidthEl = copySliderWidthEl; }} style={{width: '100 %'}} />
        </div>
      </Fragment>
    );
  }
}

export default TimeSlider;
