import ReactDOM from 'react-dom';
import React from 'react';
import {withRouter} from 'react-router-dom';
import {toJS} from 'mobx';
import {observer} from 'mobx-react';
import {Map as LeafletMap, TileLayer, ZoomControl} from 'react-leaflet';
import L from 'leaflet';
import Location from 'components/Location';
import MapOperation from 'components/MapOperation';
import {verifylonglat, editAttributeFn} from 'common/Utils';
import constants from 'common/utils/constants';
import msg from 'common/Locale';
// import { historyPush } from 'common/utils/utils';
import weatherStore from '../../pages/Weather/models/WeatherStore.js';
import LinearHeatmapLayer from './Heatmap';
import Particle from './Particle';
import {initPolyline, initSubstation} from './Utils';
import 'leaflet/dist/leaflet.css';
import './index.css';
import {getUrlBase} from '../../common/utils/uris.js';

const classPrefix = 'map-container';
let lang = '';
switch (msg.lang) {
case 'en':
  lang = 'hl=en-us';
  break;
case 'zh':
default:
  lang = 'gl=cn';
}

const corner1 = L.latLng(90, -1800);
const corner2 = L.latLng(-90, 1800);
const bounds = L.latLngBounds(corner1, corner2);

@observer
class Weather extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      zoom: 3,
      center: [32, 118],
      zoomPostion: 'bottomright',
      hideSearchBool: constants.mode !== constants.modeList[1]
    };
    this.setOverlayInfoFromParent = this.setOverlayInfoFromParent.bind(this);
    this.setFromFarmStatus = this.setFromFarmStatus.bind(this);
    this.setCenter = this.setCenter.bind(this);
    this.locationHandle = this.locationHandle.bind(this);
    this.resetCenterMapHandle = this.resetCenterMapHandle.bind(this);
    this.searchAddressHandle = this.searchAddressHandle.bind(this);
    this.mapAdjustmentHandle = this.mapAdjustmentHandle.bind(this);
  }

  componentDidMount() {
    const {fromSearchStatus, fromFarmStatus} = this.props.weatherStore;
    this.props.weatherRefHandle(this); // 将当前组件this传递给父组件
    if (fromSearchStatus || fromFarmStatus) {
      this.mapAdjustmentHandle();
    }
  }

  componentDidUpdate() {
    if (constants.mode === constants.modeList[0]) {
      initPolyline(this.leafletRef.leafletElement);
      initSubstation(this.leafletRef.leafletElement);
    }
  }

  setOverlayInfoFromParent(param) {
    this.props.weatherStore.setCurrentInfo(param);
  }

  setFromFarmStatus(bool) {
    this.props.weatherStore.setFromFarmStatus(bool);
  }

  setCenter(center) {
    const gdMapInstance = this.leafletRef.leafletElement;
    gdMapInstance.setView(center, this.props.zoom);
  }

  locationHandle(lon, lat) {
    if (verifylonglat(lon, lat)) {
      const newInfo = Object.assign(toJS(this.props.weatherStore.currentInfo), {});
      const lonLat = {lat, lng: lon};
      editAttributeFn('coordinates.latLng', lonLat, newInfo);
      this.props.weatherStore.setCurrentInfo(newInfo);
      this.setCenter([lat, lon]);
    } else {
      /* eslint-disable */
      alert('请核对经纬度是否正确');
      /* eslint-disable */
    }
  }

  resetCenterMapHandle() {
    const [lat, lon] = this.props.originCenter;
    this.locationHandle(lon, lat);
    this.mapAdjustmentHandle();
  }

  searchAddressHandle() {
    // window.location.hash = '#/addressList';
    this.props.history.push(`/addressList/${constants.mode}`);
  }

  mapAdjustmentHandle() {
    const {fromSearchStatus, selectedLocation, currentInfo} = this.props.weatherStore;
    const gdMapInstance = this.leafletRef.leafletElement;
    let latlng;
    if (fromSearchStatus) {
      latlng = L.latLng(selectedLocation.lat, selectedLocation.lon);
    } else {
      latlng = L.latLng(currentInfo.coordinates.latLng.lat, currentInfo.coordinates.latLng.lng);
    }
    gdMapInstance.setView(latlng, this.props.zoom);
    setTimeout(() => {
      const dis = this.getPageDis();
      gdMapInstance.panBy(L.point(0, dis));
      this.props.weatherStore.setFromSearchStatus(false); // 从地址搜索进入地图定位之后将此状态置为false
    }, 500); // 此处设置的500ms，保证在地图移动到了center之后再移动到中心.
  }

  // 获取定位偏移值
  getPageDis = () => {
    const deviceHeight = window.innerHeight;
    /* eslint-disable */
    const weatherElHeight = ReactDOM.findDOMNode(this.props.weatherPanelRef).offsetHeight;
    /* eslint-disable */
    const moveDis = (deviceHeight - weatherElHeight) / 2;
    const deviceHalfToWeatherDis = deviceHeight / 2 - weatherElHeight;
    return (moveDis - deviceHalfToWeatherDis);
  }

  render () {
    const { currentOverlayName, currentInfo, particleAnimationOpen, currentMapMode } = this.props.weatherStore;
    const { center, zoom, heatData, particleData } = this.props;
    const { zoom: stateZoom, center: stateCenter, zoomPostion, hideSearchBool } = this.state;
    return (
      <div className={classPrefix}>
        <div style={{ display: 'none' }}>
          {/* 定位经纬度功能隐藏 */}
          <Location locationHandle={this.locationHandle} />
        </div>
        <MapOperation
          hideSearchBool={hideSearchBool}
          searchAddressHandle={this.searchAddressHandle}
          resetCenterMapHandle={this.resetCenterMapHandle}
        />
        <LeafletMap
          ref={(leafletRef) => {
            this.leafletRef = leafletRef;
          }}
          center={center || stateCenter}
          zoom={zoom || stateZoom}
          zoomControl={false}
          minZoom={3}
          maxZoom={19}
          maxBounds={bounds}
        // onViewportChanged={viewport => this.setState({zoom: viewport.zoom})}
        >
          {heatData ? (
            <LinearHeatmapLayer
              {...this.props}
              currentOverlayName={currentOverlayName}
              currentInfo={currentInfo}
              particleAnimationOpen={particleAnimationOpen}
              setOverlayInfoFromParent={this.setOverlayInfoFromParent}
              setFromFarmStatus={this.setFromFarmStatus}
            />
          ) : null}
          {particleData ? <Particle {...this.props} currentInfo={currentInfo} opacity={this.props.particleOpcity} setOverlayInfoFromParent={this.setOverlayInfoFromParent} /> : null}
          <TileLayer
            maxNativeZoom={17}
            url={`
            ${currentMapMode ? 'http://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}' : `${getUrlBase()}/googlecn/maps/vt?lyrs=m@189&gl=cn&x={x}&y={y}&z={z}&${lang}`}`}
            // https://{s}.google.cn/vt/lyrs=p&hl=en-US&x={x}&y={y}&z={z}
          // url='http://www.google.cn/maps/vt?lyrs=m@189&gl=cn&x={x}&y={y}&z={z}'
          // http://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x} //行政地图
          // url='http://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}' // 山地图
          // url='http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineCommunity/MapServer/tile/{z}/{y}/{x}' // 地形图
          // url='http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetPurplishBlue/MapServer/tile/{z}/{y}/{x}' // 深色地形图，除了中国，其他地方没有省级边界
          // url='http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetGray/MapServer/tile/{z}/{y}/{x}'
          // url='http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetWarm/MapServer/tile/{z}/{y}/{x}' // 中国有市级行政区域，其他地方则只有国家级
          // url='http://{s}.tile.osm.org/{z}/{x}/{y}.png' // 中国地区是中文，其他地区是他们自己的语言，有省级行政区域，南海没有画9段线
          />
          {/* <Marker position={center || stateCenter}>
            <Popup>
              {'A pretty CSS3 popup.'}
              <br />
              {'Easily customizable.'}
            </Popup>
          </Marker> */}
          <ZoomControl position={zoomPostion} />
        </LeafletMap>
      </div>
    );
  }
}

export default withRouter(props => <Weather {...props} weatherStore={weatherStore} />);
