// 获取cookie
export function getCookie(name) {
  const start = document.cookie.indexOf(`${name}=`);
  const len = start + name.length + 1;
  if ((!start) && (name !== document.cookie.substring(0, name.length))) {
    return null;
  }
  if (start === -1) return null;
  let end = document.cookie.indexOf(';', len);
  if (end === -1) {
    end = document.cookie.length;
  }
  return unescape(document.cookie.substring(len, end));
}


/**
 * 设置cookie
 * @param {cookie名} name
 * @param {cookie 值} days
 * @param {路径} path
 */
export function setCookie(name, value, days = 30, path = '/') {
  const exp = new Date();
  exp.setTime(exp.getTime() + days * 24 * 60 * 60 * 1000);
  document.cookie = `${name}=${escape(value)};expires=${exp.toGMTString()}; path=${path}`;
}

export function fmtFullDate(date) {
  const y = date.getFullYear();
  let m = date.getMonth() + 1;
  m = m < 10 ? `0${m}` : m;
  let d = date.getDate();
  d = d < 10 ? (`0${d}`) : d;
  let h = date.getHours();
  h = h < 10 ? `0${h}` : h;
  let o = date.getMinutes();
  o = o < 10 ? `0${o}` : o;
  let s = date.getSeconds();
  s = s < 10 ? (`0${s}`) : s;
  return `${y}-${m}-${d} ${h}:${o}:${s}`;
}


/**
 * 获取永久化数据， 数据是字符串，如果原数据是object，就需要通过JSON.parse转换为对象
 * 如果sharedDataApi中的promise一直不结束（状态一直为pending）， 则超过2s默认为失败，调用reject函数
 * @param {key} key
 */

export function getPersistentData(key) {
  return new Promise((resolve, reject) => {
    if (device.isMobile) {
      // sharedDataApi里的api一直不给返回，导致promise一直不结束，所以加上这段时间限制，如果2s不返回就认为接口出错
      let isFinish = false;
      sharedDataApi.getShellPersistentData(key).then((data) => {
        isFinish = true;
        if (resolve !== undefined) {
          resolve(data);
        }
      }).catch((e) => {
        isFinish = true;
        console.log(`get from mobile for ${key} error`);
        if (reject !== undefined) {
          reject(e);
        }
      });
      setTimeout(() => {
        if (!isFinish) {
          if (reject !== undefined) {
            reject('time out');
          }
        }
      }, 2000);
    } else {
      const data = window.localStorage.getItem(key);
      if (data) {
        if (resolve !== undefined) {
          resolve(data);
        }
      } else {
        console.log(`${key} dosen't exit in localstoragy`);
        if (reject !== undefined) {
          reject(`${key} dosen't exit in localstoragy`);
        }
      }
    }
  });
}


/**
 * 删除永久数据， 数据是字符串，如果原数据是object，就需要通过JSON.parse转换为对象
 * 如果sharedDataApi中的promise一直不结束（状态一直为pending）， 则超过2s默认为失败，调用reject函数
 * @param {key} key
 */

export function removePersistentData(key) {
  return new Promise((resolve, reject) => {
    if (device.isMobile) {
      // sharedDataApi里的api一直不给返回，导致promise一直不结束，所以加上这段时间限制，如果2s不返回就认为接口出错
      let isFinish = false;
      sharedDataApi.removeShellPersistentData(key).then(() => {
        isFinish = true;
        resolve();
      }).catch((e) => {
        isFinish = true;
        reject(e);
      });
      setTimeout(() => {
        if (!isFinish) {
          if (reject !== undefined) {
            reject('time out');
          }
        }
      }, 2000);
    } else {
      window.localStorage.removeItem(key);
      resolve();
    }
  });
}

/**
 * 存储永久化数据， value需要是string类型
 * 如果sharedDataApi中的promise一直不结束（状态一直为pending）， 则超过2s默认为失败，调用reject函数
 * @param {key} key
 * @param {key} value    如果原value是object，需要经过JSON.stringfy转换
 */
export function persistData(key, value) {
  return new Promise((resolve, reject) => {
    if (!device.isMobile) {
      window.localStorage.setItem(key, value);
      resolve();
    } else {
      // sharedDataApi里的api一直不给返回，导致promise一直不结束，所以加上这段时间限制，如果2s不返回就认为接口出错
      let isFinish = false;
      sharedDataApi.saveShellPersistentData(key, value).then(() => {
        isFinish = true;
        resolve();
      }).catch((e) => {
        isFinish = true;
        reject(e);
      });
      setTimeout(() => {
        if (!isFinish) {
          if (reject !== undefined) {
            reject('time out');
          }
        }
      }, 2000);
    }
  });
}

export function getFormatDate(utc) {
  const date = new Date(utc);
  const year = date.getFullYear();
  let month = `${date.getMonth() + 1}`;
  month = month.length === 1 ? `0${month}` : month;
  let day = `${date.getDate()}`;
  day = day.length === 1 ? `0${day}` : day;
  let hour = `${date.getHours()}`;
  hour = hour.length === 1 ? `0${hour}` : hour;
  let minute = `${date.getMinutes()}`;
  minute = minute.length === 1 ? `0${minute}` : minute;
  return `${year}-${month}-${day} ${hour}:${minute}`;
}

export function setStorage(item, value) {
  if (constants.ENV === 'weixin') {
    return new Promise((resolve, reject) => {
      window.localStorage.setItem(item, JSON.stringify(value));
      resolve();
    });
  }
  return sharedDataApi.saveShellPersistentData(item, JSON.stringify(value));
}

export function setStorageUnSty(item, value) {
  if (constants.ENV === 'weixin') {
    return new Promise((resolve, reject) => {
      window.localStorage.setItem(item, value);
      resolve();
    });
  }
  return sharedDataApi.saveShellPersistentData(item, value);
}

export function getStorage(item) {
  if (constants.ENV === 'weixin') {
    return new Promise((resolve, reject) => {
      const data = window.localStorage.getItem(item);
      if (data) {
        if (resolve !== undefined) {
          resolve(JSON.parse(data));
        }
      } else {
        console.log(`${item} dosen't exit in localstoragy`);
        if (reject !== undefined) {
          reject(`${item} dosen't exit in localstoragy`);
        }
      }
    });
  }
  return sharedDataApi.getShellPersistentData(item).then((data) => {
    if (data !== undefined) {
      return JSON.parse(data);
    }
    return undefined;
  });
}

export function getStorageUnparse(item) {
  if (constants.ENV === 'weixin') {
    return new Promise((resolve, reject) => {
      const data = window.localStorage.getItem(item);
      if (data) {
        if (resolve !== undefined) {
          resolve(data);
        }
      } else if (resolve !== undefined) {
        resolve(undefined);
      }
    });
  }
  return sharedDataApi.getShellPersistentData(item).then((data) => {
    if (data !== undefined) {
      return data;
    }
    return undefined;
  }).catch(() => {
    return undefined
  });
}

export function removeStorage(item) {
  if (constants.ENV === 'weixin') {
    if (!window.localStorage) {
      console.log('浏览器不支持localstorage');
      return false;
    }
    const storage = window.localStorage;
    storage.removeItem(item);
  } else {
    sharedDataApi.removeShellPersistentData(item);
  }
}

export function historyPush(url) {
  if (constants.ENV !== 'weixin') {
    nativeHistory.push(url);
  } else {
    window.location.href = url;
  }
  // console.log(url.substring(12));
  // window.location.href = url.substring(12);
}

export function historyBack() {
  if (constants.ENV !== 'weixin') {
    nativeHistory.goBack();
  } else {
    window.history.back();
  }
  // window.location.href = document.referrer;
}

export function setTreeData(dataList) {
  if (!dataList || dataList.length === 0) {
    return [];
  }
  const data = dataList.map((d) => {
    d.attributes.title = d.attributes.name;
    d.attributes.key = d.attributes.objectID;
    return d.attributes;
  });
  let tree = data.filter((father) => { // 循环所有项
    const branchArr = [];
    data.map((child) => {
      if (father.objectID === child.parentID) {
        if (child.businessType.indexOf('21') < 0) {
          return;
        } if (child.objectTypeID === '58') {
          const site = data.filter(c => child.objectID === c.parentID && (!c.objectTypeID === '102' || c.businessType.indexOf('21') >= 0));
          if (site && site.length > 1) {
            branchArr.push(child);
            return;
          }
          if (site && site.length === 1) {
            branchArr.push(site[0]);
            return;
          }
        }
        branchArr.push(child); // 返回每一项的子级数组
      }
    });
    if (branchArr.length > 0) {
      father.children = branchArr; // 如果存在子级，则给父级添加一个children属性，并赋值
    }
    return father.parentID === ''; // 返回第一层
  });
  if (tree.length === 1 && tree[0].children) tree = tree[0].children;
  if (tree.length === 1 && tree[0].children) tree = tree[0].children;

  return tree; // 返回树形数据
}
export function fmtDaywind(wind) {
  if (language.indexOf('en') > -1) {
    switch (wind) {
    case '东': return 'E';
    case '西': return 'W';
    case '南': return 'S';
    case '北': return 'N';
    case '东南': return 'SE';
    case '东北': return 'NE';
    case '西南': return 'SW';
    case '西北': return 'NW';
    default: return '--';
    }
  } else return wind;
}

export function debounce(fn, wait = 1000, immediately) {
  let timer = null;
  return function() {
    if (immediately) {
      fn.apply(this, arguments);
    }
    if (timer) { clearTimeout(timer); }
    timer = setTimeout(() => {
      fn.apply(this, arguments);
    }, wait);
  };
}

export function throttling(fn, wait = 1000) {
  let pre = new Date().getTime();
  return function() {
    if (new Date().getTime() - pre > wait) {
      fn.apply(this, arguments);
      pre = new Date().getTime();
    }
  };
}

export function getSiteList(root) {
  const result = [];
  const temp = [root];
  while (temp.length > 0) {
      const node = temp.shift();
      if (node) {
          if (node.children) {
              temp.push(...node.children);
          } else {
              result.push(node);
          }
      }
  }
  return result;
}

export function getSiteId(root, result = []) {
  root.forEach((item) => {
    if (item.children) {
      result.push(item.id);
      result.concat(...getSiteId(item.children, result));
    } else {
      result.push(item.id);
    }
  });
  return result;
}