import i18n from 'common/i18n';
import {WIND_OVERLAY, TEMP_OVERLAY, HUMIDITY_OVERLAY, RAIN_OVERLAY} from '../../pages/Weather/constants';

export const getUnit = (overlayName) => {
  switch (overlayName) {
  case WIND_OVERLAY:
    return 'm/s';
  case TEMP_OVERLAY:
    return '°C';
  case HUMIDITY_OVERLAY:
    return '%';
  case RAIN_OVERLAY:
    return 'mm';
  default:
    return '';
  }
};

export const getFixedNum = (overlayName, num) => {
  if (num === null) {
    return 'no data ';
  }
  if (num.toString() === 'NaN') {
    return 'NaN';
  }
  if (num === 0) {
    return 0;
  }
  switch (overlayName) {
  case WIND_OVERLAY:
    return num && num.toFixed(1);
  case TEMP_OVERLAY:
    return num && num.toFixed(1);
  case RAIN_OVERLAY:
    return num && num.toFixed(2);
  default:
    return num && num.toFixed(0);
  }
};

export const getWindDirection = (angle) => {
  const angleNum = parseInt(angle);
  let windDAStr = '';
  switch (true) {
  case angleNum === 0 || angleNum === 360:
    windDAStr = i18n.WD_NORTH;
    break;
  case angleNum > 0 && angleNum < 90:
    windDAStr = i18n.WD_EAST_NORTH;
    break;
  case angleNum === 90:
    windDAStr = i18n.WD_EAST;
    break;
  case angleNum > 90 && angleNum < 180:
    windDAStr = i18n.WD_EAST_SOUTH;
    break;
  case angleNum === 180:
    windDAStr = i18n.WD_SOUTH;
    break;
  case angleNum > 180 && angleNum < 270:
    windDAStr = i18n.WD_WEST_SOUTH;
    break;
  case angleNum === 270:
    windDAStr = i18n.WD_WEST;
    break;
  case angleNum > 270 && angleNum < 360:
    windDAStr = i18n.WD_WEST_NORTH;
    break;
  default:
    windDAStr = '';
  }
  return windDAStr;
};

export function getI18nOverlay(overlayName) {
  switch (overlayName) {
  case WIND_OVERLAY:
    return i18n.WIND_TIP;
  case TEMP_OVERLAY:
    return i18n.TEMP_TIP;
  case HUMIDITY_OVERLAY:
    return i18n.HUMIDITY_TIP;
  case RAIN_OVERLAY:
    return i18n.RAIN_TIP;
  default:
    return '';
  }
}

export function getI18nCurrentName(overlayName) {
  switch (overlayName) {
  case WIND_OVERLAY:
    return i18n.WIND;
  case TEMP_OVERLAY:
    return i18n.TEMP;
  case HUMIDITY_OVERLAY:
    return i18n.HUMIDITY;
  case RAIN_OVERLAY:
    return i18n.RAIN;
  default:
    return '';
  }
}
