import React, {Component} from 'react';
import './index.css';

let isDown = false;
let x = 0;
let l = 0;
let newLeft = 0;
class Tooltip extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rightDis: 0
    };
    this.mdownHandle = this.mdownHandle.bind(this);
    this.mmoveHandle = this.mmoveHandle.bind(this);
    this.mupHandle = this.mupHandle.bind(this);
    // this.disAdjuestHandle = this.disAdjuestHandle.bind(this);
  }

  componentDidMount() {
    document.addEventListener('mousedown', this.mdownHandle);
    document.addEventListener('mousemove', this.mmoveHandle);
    document.addEventListener('mouseup', this.mupHandle);
    document.addEventListener('touchstart', this.mdownHandle);
    document.addEventListener('touchmove', this.mmoveHandle);
    document.addEventListener('touchend', this.mupHandle);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.mdownHandle);
    document.removeEventListener('mousemove', this.mmoveHandle);
    document.removeEventListener('mouseup', this.mupHandle);
    document.removeEventListener('touchstart', this.mdownHandle);
    document.removeEventListener('touchmove', this.mmoveHandle);
    document.removeEventListener('touchend', this.mupHandle);
  }

  mdownHandle(e) {
    // TODO
    // 刚开始没有计算元素的坐标与点击事件位置之间的差值，直接将鼠标移动的位置赋值给元素的位置（top和left属性），这样用户会感觉到鼠标跳动了一下，并且随着鼠标的移动，元素一直在鼠标的右下方。
    // 这个问题的解决办法是：在触发mousedown的时候除了设置被拖拽的事件，也要记录拖拽对象的位置与事件发生的位置的水平和垂直之间的差值，触发mousemove事件的时候将拖拽对象的位置设置为事件发生的水平和垂直位置对应减去差值。
    if (e.target.className !== 'tooltip_inner') {
      return;
    }
    x = e.clientX || e.touches[0].clientX;
    l = this.tooltipEL.getBoundingClientRect().left;
    isDown = true;
  }

  mmoveHandle(e) {
    if (!isDown) {
      return;
    }
    // 获取x和y
    const nx = e.clientX || e.touches[0].clientX;
    const dis = nx - (x - l);
    const newDis = dis - this.innerRef.getBoundingClientRect().width / 2;
    const nl = Math.max(0, newDis);
    newLeft = Math.min(nl, this.props.sliderOffset.width);
    this.props.setMouseMoveFromTooltip();
    this.props.setProgressHandle(newLeft / this.props.sliderOffset.width);
    this.disAdjuestHandle(newLeft / this.props.sliderOffset.width);
  }

  mupHandle() {
    isDown = false;
    this.props.setMouseUpFromTooltip(newLeft / this.props.sliderOffset.width);
  }

  disAdjuestHandle(percent) {
    setTimeout(() => {
      // 此处加定时器因为，在打开自动播放之后，每次时间的前进都会触发setValue，继而触发此处的formatter函数，但是此时进度条的宽度还没有随之响应，所以立即获取到的进度条宽度还是未改变之前的宽度，所以加20毫秒的延迟执行获取已经变化后的进度条宽度。
      const spDis = this.surplusDis(percent);
      if (spDis <= 20) {
        const rightDis = 20 - spDis;
        this.setState({
          rightDis
        });
      } else {
        this.setState({
          rightDis: 0
        });
      }
    }, 20);
  }

  surplusDis = (percent) => {
    // 计算当前滚动条剩余的距离。
    const {width: playWidth} = this.props.playBtnComponent.getBoundingClientRect();
    const progressTotalWidth = window.innerWidth - playWidth;
    const processCurWidth = progressTotalWidth - progressTotalWidth * percent;
    return processCurWidth;
  };

  render() {
    const left = this.props.progressScale * this.props.sliderOffset.width;
    const {rightDis} = this.state;
    return (
      <div
        className='tooltipBox'
        style={{left: `${left}px`}}
        ref={(tooltipEL) => {
          this.tooltipEL = tooltipEL;
        }}
      >
        <span className='tooltip_inner' style={{position: 'relative', right: `${rightDis}px`}} ref={(ref) => { this.innerRef = ref; }}>
          {this.props.toolTipVal}
        </span>
        <span className='tooltip_arrow' />
      </div>
    );
  }
}

export default Tooltip;
