/**
 * 20190106
 * by zuoyong.tang
 * 对dpl的localeUtils封装，拆分成2个方法，分别加载UI和message的国际化资源，且可以分别再被替换
 * message的优先级高于UI
*/

import * as utils from '@enos/dpl/lib/locale/localeUtils';

export * from '@enos/dpl/lib/locale/localeUtils';
export {default} from '@enos/dpl/lib/locale/localeUtils';

let i18nUIs = {};
let i18nMessages = {};

function extendObj(src, exd) {
  if (!exd) return src;
  if (!src) return exd;
  const temp = JSON.parse(JSON.stringify(src));
  Object.keys(exd).forEach((k) => {
    const v = exd[k];
    if (v instanceof Object) {
      temp[k] = extendObj(temp[k], v);
    } else {
      temp[k] = v;
    }
  });
  return temp;
}

function init(res) {
  const tempLocale = utils.getLocale();
  utils.setLocale();
  utils.init(res);
  if (tempLocale) {
    utils.setLocale(tempLocale);
  }
}

function initUI(...args) {
  i18nUIs = args[0] || {};
  init(extendObj(i18nUIs, i18nMessages));
}

function initMessage(...args) {
  i18nMessages = args[0] || {};
  init(extendObj(i18nUIs, i18nMessages));
}

function plugin(p) {
  if (typeof p === 'function') {
    const {i18nUIs: ui, i18nMessages: msg} = p(i18nUIs, i18nMessages) || {};
    if (ui || msg) {
      if (ui) initUI(ui);
      if (msg) initMessage(msg);
      // init(extendObj(ui, msg));
    }
  }
}

const getLocale = utils.getLocale;

export {initUI, initMessage, plugin, getLocale};
