import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './index.less';

const prefixCls = 'message';

export default class Message extends Component {
  static propTypes = {
    visible: PropTypes.bool,
    message: PropTypes.string
  };

  static defaultProps = {
    visible: false,
    message: ''
  };

  // constructor(props) {
  //   super(props);
  // }


  render() {
    const {visible, message} = this.props;
    const screenHeight = window.screen.height;
    return (
      <div className={`${prefixCls} ${visible ? 'show' : 'hide'}`}>
        <div className='progress-bar' style={{top: `${screenHeight / 2}px`}}>
          <div className='content'>{typeof message === 'object' ? JSON.stringify(message) : `${message}`}</div>
        </div>
      </div>
    );
  }
}
