import React, {Component} from 'react';
// import i18n from 'common/i18n';
import constants from 'common/utils/constants';
import {extendedSinebowColor, segmentedColorScale} from '../Weather/Utils';
import {WIND_OVERLAY, TEMP_OVERLAY, HUMIDITY_OVERLAY, RAIN_OVERLAY} from '../../pages/Weather/constants';
import './index.css';

/**
 * 图例第一节颜色的宽度
 */
const firstw = 22;
const lastw = 2;

class ColorScale extends Component {
  constructor(props) {
    super(props);
    this.getOverlayInfo = this.getOverlayInfo.bind(this);
    this.drawScaleCanvas = this.drawScaleCanvas.bind(this);
    this.getScaleNum = this.getScaleNum.bind(this);
  }

  componentDidMount() {
    // this.drawScaleCanvas();
  }

  componentWillReceiveProps() {
    // this.drawScaleCanvas();
  }

  drawScaleCanvas() {
    const {currentOverlay, scaleNumBound} = this.props;
    this.parseGradient(this.props.gradientScale || this.props.gradient);
    const c = this.scaleCanvas;
    const n = c.width - 1;
    const g = c.getContext('2d');
    const wdarr = [];
    const interval = n / (scaleNumBound.length - 1);
    if (currentOverlay === RAIN_OVERLAY) {
      this.parseGradient(this.props.gradient);
      scaleNumBound.forEach((v, i) => {
        wdarr.push(i * interval);
      });
    }
    for (let i = 0; i <= n; i++) {
      let v;
      if (currentOverlay === RAIN_OVERLAY) {
        let ind;
        wdarr.forEach((_, j) => {
          if (ind === undefined && i <= j * interval) {
            ind = j - 1;
          }
        });
        if (ind < 0) {
          ind = 0;
        }
        v = (scaleNumBound[ind + 1] - scaleNumBound[ind]) * ((i - ind * interval) / interval) + scaleNumBound[ind];
      } else {
        v = this.spread(i / n, this.props.valueBound[0], this.props.valueBound[1]);
      }
      const rgb = this._gradientFn(v, 1);
      g.fillStyle = `rgb(${rgb[0]},${rgb[1]},${rgb[2]})`;
      g.fillRect(i, 0, 1, 22);
    }
  }

  spread = (p, low, high) => this.getValuePosition(p * (high - low) + low);

  getValuePosition(v) {
    const {valueBound} = this.props;
    return (v - valueBound[0]) / (valueBound[1] - valueBound[0]);
  }

  parseGradient = (gradient) => {
    if (!gradient) {
      this._gradient = gradient;
      this._gradientFn = extendedSinebowColor;
    } else if (this._gradient !== gradient) {
      this._gradient = gradient;
      this._gradientFn = segmentedColorScale(
        Object.keys(gradient)
          .sort((a, b) => Number(a) - Number(b))
          .map(k => [
            Number(k),
            gradient[k]
              .split(/[(),]/)
              .slice(1, 4)
              .map(Number)
          ])
      );
    }
  };

  getOverlayInfo() {
    switch (this.props.currentOverlay) {
    case WIND_OVERLAY:
      // return `${i18n.WIND} (m/s)`;
      return 'm/s';
    case TEMP_OVERLAY:
      return '°C';
      // return `${i18n.TEMP} (°C)`;
    case HUMIDITY_OVERLAY:
      // return `${i18n.HUMIDITY} (%)`;
      return '%';
    case RAIN_OVERLAY:
      // return `${i18n.RAIN_COLORSCALE} (mm)`;
      return 'mm';
    default:
      // return `${i18n.WIND} (m/s)`;
      return 'm/s';
    }
  }

  getScaleNum() {
    if (this.props.scaleNumBound.length !== 0) {
      return this.props.scaleNumBound.map(item => <span key={item}>{item}</span>);
    }
    const len = 6;
    const high = this.props.valueBound[this.props.valueBound.length - 1];
    const low = this.props.valueBound[0];
    return Array.from({length: len}).map((item, index) => {
      const num = Math.floor((index / (len - 1)) * (high - low) + low);
      return <span key={num}>{num}</span>;
    });
  }

  getBgCss = () => {
    const colorSet = this.props.gradient || this.props.gradientScale;

    const keys = Object.keys(colorSet);
    const step = (100 - firstw - lastw) / (keys.length - 2);

    const colors = keys.sort((a, b) => a - b).map((v, i) => {
      const c = colorSet[v];
      const w = i === 0 ? 0 : (firstw + (i - 1) * step);
      return {c, w: w.toFixed(5)};
    });

    return `linear-gradient(to right, ${colors.map(o => `${o.c} ${o.w}%`)})`;
  }

  getNumText = () => {
    const {scaleNumBound, valueBound} = this.props;
    const dom = [];
    let arr = scaleNumBound;
    if (!scaleNumBound.length) {
      const len = 6;
      const high = valueBound[valueBound.length - 1];
      const low = valueBound[0];
      arr = Array.from({length: len}).map((item, index) => Math.floor((index / (len - 1)) * (high - low) + low));
    }
    const hideNum = 0;// (arr.length > 8 ? 2 : 1) - 1;
    const step = (100 - firstw - lastw) / (arr.length - 2);
    arr.forEach((v, i) => {
      if (i <= hideNum) return;
      const left = firstw + (i - 1) * step;
      dom.push(<b style={{left: `${left}%`}}>{v}</b>);
    });
    return dom;
  }

  render() {
    return (
      <div className='scaleBox'>
        {/* <canvas
          className='scaleCanvas'
          width={362}
          height={22}
          ref={(scaleCanvas) => {
            this.scaleCanvas = scaleCanvas;
          }}
        /> */}
        <div className='scaleBg' style={{background: this.getBgCss()}} />
        <span className='overlayInfo'>{this.getOverlayInfo()}</span>
        <div className={((constants.mode !== constants.modeList[2]) && (this.props.currentOverlay === RAIN_OVERLAY)) ? 'scaleNum isRain' : 'scaleNum'}>{this.getNumText()}</div>
      </div>
    );
  }
}

export default ColorScale;
