import React, {Component} from 'react';
import './index.css';

class Location extends Component {
  // constructor(props) {
  //   super(props);
  // }

  render() {
    return (
      <div className='locationArea'>
        <div className='inputDegBox'>
          <p>Lon：</p>
          <input
            type='text'
            className='inputVal'
            ref={(Ref) => {
              this.lonRef = Ref;
            }}
          />
        </div>
        <div className='inputDegBox'>
          <p>Lat：</p>
          <input
            type='text'
            className='inputVal'
            ref={(Ref) => {
              this.latRef = Ref;
            }}
          />
        </div>
        <input
          className='toPosBtn'
          onClick={() => {
            this.props.locationHandle(this.lonRef.value, this.latRef.value);
          }}
          value='Positioning'
          type='button'
        />
      </div>
    );
  }
}
export default Location;
