/**
 * 20190731 by zuoyong.tang
 * react-leaflet的overlay插件 -- 基类
 * 该基类不负责overlay的绘制，只解析weather特性的数据
 * 并在数据变更或者地图操作之后触发overlay的重绘
 */

/* eslint-disable no-multi-assign */
/* eslint-disable no-underscore-dangle */

import L from 'leaflet';
import {MapLayer} from 'react-leaflet';
import PropTypes from 'prop-types';
import constants from 'common/utils/constants';

import './index.css';

function safeRemoveLayer(leafletMap, el) {
  const {overlayPane} = leafletMap.getPanes();
  if (overlayPane && overlayPane.contains(el)) {
    overlayPane.removeChild(el);
  }
}

const LAT_MIN = -85.055;
const LAT_MAX = 85.06;
const LNG_MIN_G = -1800;
const LNG_MAX_G = 1800;
const LNG_MIN = -180;
const LNG_MAX = 180;

//  for (let i = 90; i >= -90; i--) {
//     for (let j = -180; j < 180; j++) {
//       const index = (90 - i) * 360 + 180 + j;
export default class WeatherMapLayer extends MapLayer {
  static propTypes = {
    data: PropTypes.array.isRequired, // 包含两个元素，分别为两个x和y方向上的列表，比如全球的数据以65160长度的数组，表示181个纬度，每个纬度360个经度的值
    header: PropTypes.object.isRequired, // 数据的描述信息，包括经纬度范围，步长等
    valueBound: PropTypes.array
  };

  static defaultProps = {
    valueBound: [0, 1]
  };

  createLeafletElement() {
    return null;
  }

  paraentParticleData = null;

  constructor(props) {
    super(props);
    this._el = null;
    this._data = null;
    this._grid = null;
    this._header = null;
    this._latLngCache = [];
    this._zoom = 0;
    this._frame = null;
    this._particalData = null;
    this._heatData = null;
    this._newParticle = null;
  }

  // todo，数据输入一定是这样的格式吗
  parseData(data, header) {
    let isNormalHeat = false;
    let normalData = null;
    let uData = null;
    let vData = null;
    if (data && this._data !== data) {
      this._data = data;
      this._header = header;
      if (data.length === 1) {
        // 普通图层
        isNormalHeat = true;
        normalData = data[0];
      } else {
        uData = data[0];
        vData = data[1];
      }
      const ni = header.nx;
      const nj = header.ny; // number of grid points W-E and N-S (e.g., 144 x 73)
      // Scan mode 0 assumed. Longitude increases from λ0, and latitude decreases from φ0.
      // http://www.nco.ncep.noaa.gov/pmb/docs/grib2/grib2_table3-4.shtml
      const grid = [];
      let p = 0;
      for (let j = 0; j < nj; j++) {
        for (let i = 0; i < ni; i++, p++) {
          grid[p] = isNormalHeat ? normalData[p] : [uData[p], vData[p]];
        }
      }
      this._grid = grid;
    }
  }

  getParticleData() {
    return this.paraentParticleData;
  }

  componentDidMount() {
    const canAnimate = this.props.leaflet.map.options.zoomAnimation && L.Browser.any3d;
    const zoomClass = `leaflet-weather-layer leaflet-zoom-${canAnimate ? 'animated' : 'hide'}`;
    const transformProp = L.DomUtil.testProp(['transformOrigin', 'WebkitTransformOrigin', 'msTransformOrigin']);

    this._el = L.DomUtil.create('canvas', zoomClass);
    this._el.style[transformProp] = '50% 50%';

    const el = this._el;

    const Heatmap = L.Layer.extend({
      onAdd: leafletMap => leafletMap.getPanes().overlayPane.appendChild(el),
      addTo: (leafletMap) => {
        leafletMap.addLayer(this);
        return this;
      },
      onRemove: leafletMap => safeRemoveLayer(leafletMap, el)
    });

    // leafletElement是MapLayer基类上的属性
    this.leafletElement = new Heatmap();
    super.componentDidMount();

    this.reset();
    this.attachEvents();
  }

  componentWillReceiveProps(nProps) {
    const cProps = this.props;

    if (nProps.heatData !== cProps.heatData || nProps.particleData !== cProps.particleData || nProps.valueBound.toString() !== cProps.valueBound.toString()) {
      this.reset();
    }
  }

  componentWillUnmount() {
    safeRemoveLayer(this.props.leaflet.map, this._el);
  }

  componentDidUpdate() {
    this.props.leaflet.map.invalidateSize();
    this.reset();
  }

  // todo 提升体验，在move的过程中需要动态渲染
  attachEvents() {
    const leafletMap = this.props.leaflet.map;
    leafletMap.on('viewreset', () => this.reset());
    leafletMap.on('move', () => this.changeLocationInfo());
    leafletMap.on('moveend', () => this.reset());
    leafletMap.on('click', (ev) => {
      this.setOverlayInfoHandle(ev);
    });
  }

  changeLocationInfo() {
    const {currentInfo} = this.props.weatherStore;
    if (currentInfo.coordinates.latLng.lng) {
      const point = this.props.leaflet.map.latLngToContainerPoint(currentInfo.coordinates.latLng);
      const obj = {
        containerPoint: point
      };
      const infoList = Object.assign(currentInfo, obj);
      this.props.setOverlayInfoFromParent(infoList);
    }
  }

  reset() {
    const topLeft = this.props.leaflet.map.containerPointToLayerPoint([0, 0]);
    L.DomUtil.setPosition(this._el, topLeft);

    const size = this.props.leaflet.map.getSize();
    this._zoom = this.props.leaflet.map.getZoom();

    if (this._el.width !== size.x) {
      this._el.width = size.x;
      this._el.style.width = `${size.x}px`;
    }
    if (this._el.height !== size.y) {
      this._el.height = size.y;
      this._el.style.height = `${size.y}px`;
    }

    if (!this._frame && !this.props.leaflet.map._animating) {
      this._latLngCache = [];
      this._frame = L.Util.requestAnimFrame(this.redraw, this);
    }
  }

  // 获取经纬度的性能不是太好，所以x和y分别缓存
  getLatLngFromCache(x, y, leafletMap) {
    const [latCache = {}, lngCache = {}] = this._latLngCache;
    const lat = latCache[y];
    const lng = lngCache[x];
    if (lat !== undefined && lng !== undefined) {
      return {lat, lng};
    }
    const latLng = leafletMap.containerPointToLatLng([x, y]);
    latCache[y] = latLng.lat;
    lngCache[x] = latLng.lng;
    this._latLngCache = [latCache, lngCache];
    return latLng;
  }

  // leaflet地图的特性，显示范围是这样的
  // latLngValid(latLng) {
  //   return (
  //     latLng.lat <= LAT_MAX
  //     && latLng.lat >= LAT_MIN
  //     && latLng.lng >= LNG_MIN
  //     && latLng.lng <= LNG_MAX
  //     && latLng.lat <= this._header.la1
  //     && latLng.lat >= this._header.la2
  //     && latLng.lng >= this._header.lo1
  //     && latLng.lng <= this._header.lo2
  //   );
  // }
  // leaflet地图的特性，显示范围是这样的
  latLngValid(latLng) {
    if (constants.mode === constants.modeList[2]) {
      return latLng.lat <= LAT_MAX && latLng.lat >= LAT_MIN
      && latLng.lng >= LNG_MIN_G && latLng.lng <= LNG_MAX_G
      && latLng.lat <= this._header.la1 && latLng.lat >= this._header.la2;
    }
    return latLng.lat <= LAT_MAX && latLng.lat >= LAT_MIN
      && latLng.lng >= LNG_MIN && latLng.lng <= LNG_MAX
      && latLng.lat <= this._header.la1 && latLng.lat >= this._header.la2 && latLng.lng >= this._header.lo1
      && latLng.lng <= this._header.lo2;
  }

  getBounds() {
    const leafletMap = this.props.leaflet.map;
    const size = leafletMap.getSize();
    let leftTop;
    let rightBottom;
    if (constants.mode === constants.modeList[2]) {
      leftTop = leafletMap.latLngToContainerPoint({lat: Math.min(this._header.la1, LAT_MAX), lng: Math.min(this._header.lo1, LNG_MIN_G)});
      rightBottom = leafletMap.latLngToContainerPoint({lat: Math.max(this._header.la2, LAT_MIN), lng: Math.max(this._header.lo2, LNG_MAX_G)});
    } else {
      leftTop = leafletMap.latLngToContainerPoint({lat: Math.min(this._header.la1, LAT_MAX), lng: Math.max(this._header.lo1, LNG_MIN)});
      rightBottom = leafletMap.latLngToContainerPoint({lat: Math.max(this._header.la2, LAT_MIN), lng: Math.min(this._header.lo2, LNG_MAX)});
    }
    const bounds = {
      xMin: Math.max(0, leftTop.x),
      xMax: Math.min(size.x, rightBottom.x),
      yMin: Math.max(0, leftTop.y),
      yMax: Math.min(size.y, rightBottom.y)
    };
    leftTop = leafletMap.containerPointToLatLng([bounds.xMin, bounds.yMin]);
    rightBottom = leafletMap.containerPointToLatLng([bounds.xMax, bounds.yMax]);
    return Object.assign(bounds, {
      width: bounds.xMax - bounds.xMin,
      height: bounds.yMax - bounds.yMin,
      latMin: leftTop.lat,
      latMax: rightBottom.lat,
      lngMin: leftTop.lng,
      lngMax: rightBottom.lng,
      latRange: rightBottom.lat - leftTop.lat,
      lngRange: rightBottom.lng - leftTop.lng
    });
  }

  redraw() {
    this.parseData(this.props.particleData, this.props.header);

    if (this._data) {
      this._frame = null;
    }
  }

  render() {
    return null;
  }
}
