import {observable, action} from 'mobx';
import i18n from 'common/i18n';

class WeatherStore {
  // 当前在地址搜索页面中选择的位置信息
  @observable selectedLocation = {};

  // 从场站中点击进入
  @observable fromFarmStatus = false;

  // 从地址进入还是自己点击的。 false : 不是 true: 是的
  @observable fromSearchStatus = false;

  // 当前点信息面板显示逻辑。 0：不显示，底部进度条显示；1：显示省略的信息面板,底部进度条隐藏；2：全屏显示信息面板,底部进度条隐藏。
  @observable showInfoPanelStatus = 0;

  // 当前地图模式
  @observable currentMapMode = 0; // 0：标准地图 1: 卫星地图

  // 显示操作面板 从右侧划出的操作面板。
  @observable showOperation = false;

  // 是否开启粒子动画
  @observable particleAnimationOpen = true;

  // 当前位置信息
  @observable currentInfo = {
    hideOverlayTip: false,
    coordinates: {
      coordText: '',
      latLng: {}
    },
    containerPoint: {},
    formatOverlayInfo: {
      windDirectionAngle: '',
      windSpeedNum: '',
      heatNum: ''
    }
  };

  // 当前进度条毫秒数，影响到接口请求的时间参数。
  @observable requestDataTime = null;

  // 当前图层数据
  @observable overlayData = new Map();

  // 当前图层
  @observable currentOverlayName = 'wind';

  // 当前数据源
  @observable currentSourceName = 'Enweather';

  // 所有的数据源
  @observable sourceList = ['Enweather', 'WRF_SARAWAK', 'IBM_ENHANCE'];

  // 所有图层 {name: 'lightning'},
  @observable overlayList = [{name: 'wind', i18nName: i18n.WIND}, {name: 'temp', i18nName: i18n.TEMP}, {name: 'humidity', i18nName: i18n.HUMIDITY}, {name: 'rain', i18nName: i18n.RAIN}];

  @action setSelectedLocation(info) {
    this.selectedLocation = info;
  }

  @action setFromFarmStatus(bool) {
    this.fromFarmStatus = bool;
  }

  @action setFromSearchStatus(bool) {
    this.fromSearchStatus = bool;
  }

  @action setShowInfoPanelStatus(statusNum) {
    this.showInfoPanelStatus = statusNum;
  }

  @action setCurrentMapMode(modeNum) {
    this.currentMapMode = modeNum;
  }

  @action setShowOperation(bool) {
    this.showOperation = bool;
  }

  @action setParticleAnimationStatus(bool) {
    this.particleAnimationOpen = bool;
  }

  @action setCurrentInfo(info) {
    this.currentInfo = info;
  }

  @action setRequestDataTime(time) {
    this.requestDataTime = time;
  }

  @action setOverlayData(data) {
    // this.overlayData = data;
    // this.overlayData.set(data.overlayName, data);
    this.overlayData.set(`${data.overlayName}`, data);
  }

  @action setOverlayName(name) {
    this.currentOverlayName = name;
  }

  @action setSourceName(source) {
    this.currentSourceName = source;
  }
}

export default new WeatherStore();
