import React, {Component} from 'react';
import {toJS} from 'mobx';
import {observer} from 'mobx-react';
import i18n from 'common/i18n';
import weatherStore from '../../pages/Weather/models/WeatherStore.js';
import * as infoUtil from './infoUtil';
import {WIND_OVERLAY} from '../../pages/Weather/constants';
import 'common/mapIcon/style.css';
import './index.css';

@observer
class CurrentInfo extends Component {
  constructor(props) {
    super(props);
    this.getTipArea = this.getTipArea.bind(this);
    this.hideOverlayTip = this.hideOverlayTip.bind(this);
    this.getOverlayNum = this.getOverlayNum.bind(this);
    this.showOperationHandle = this.showOperationHandle.bind(this);
    this.showWeatherPanelHandle = this.showWeatherPanelHandle.bind(this);
    this.boxInView = this.boxInView.bind(this);
  }

  componentDidUpdate() {
    this.boxInView();
  }

  componentWillReceiveProps() {
  }

  initCss = () => {
    const currentInfoEl = this.container.getElementsByClassName('overlayTipArea')[0];
    // 清空currentInfoEl的margin-left样式 start
    const tipCss = currentInfoEl.getAttribute('style').split(';');
    const obj = {};
    tipCss.forEach((item, index) => {
      const cur = item.split(':')[0].trim();
      if (!obj[cur]) {
        obj[cur] = 1;
      } else {
        obj[cur] += 1;
      }
      if (item.indexOf('margin-left') !== -1) {
        tipCss[index] = '';
      }
      if (obj[cur] > 1) {
        tipCss[index] = '';
      }
    });
    const newTipCss = tipCss.filter(item => (item !== ''));
    currentInfoEl.setAttribute('style', newTipCss.join(';'));
    // 清空currentInfoEl的margin-left样式 end
  }

  // 当点在屏幕可视区域内时，气泡被遮挡住左半边或右半边时，气泡做贴边处理。当气泡在上半边被遮挡时，气泡放置点下方。
  boxInView() {
    if (!this.container.getElementsByClassName('overlayTipArea')[0]) return;
    this.initCss();
    const currentInfoEl = this.container.getElementsByClassName('overlayTipArea')[0];
    const arrowEl = this.container.getElementsByClassName('overlayTipArea')[0].getElementsByClassName('arrow')[0];
    const hideInfoEl = this.container.getElementsByClassName('overlayTipArea')[0].getElementsByClassName('xIcon')[0];
    const signDotEl = this.container.getElementsByClassName('signDot')[0].getBoundingClientRect();
    const dotEl = this.container.getElementsByClassName('signDot')[0].getElementsByClassName('radius')[0];
    arrowEl.removeAttribute('style');
    const {left: boxLeft, top: boxTop, width: boxWidth, height: boxHeight} = currentInfoEl.getBoundingClientRect();
    const {left: dotLeft, top: dotTop, width: dotWidth, height: dotHeight} = dotEl.getBoundingClientRect();
    const {height: arrowHeight} = arrowEl.getBoundingClientRect();
    const {height: XHeight} = hideInfoEl.getBoundingClientRect();
    const originBoxWidth = boxWidth; // 将原来的气泡宽度保存，方便下面使用
    const boxNewWidth = boxWidth + XHeight; // 此处把X图标的宽度也算在气泡中做贴边处理。
    const deviceWidth = window.innerWidth;
    const deviceHeight = window.innerHeight;
    const topDis = (dotHeight / 2) + boxHeight + XHeight + arrowHeight;
    if ((dotLeft > -dotWidth && dotLeft < deviceWidth) && (dotTop > -dotHeight && dotTop < deviceHeight)) { // 判断点是否在可是区域内。
      if (boxLeft < 0) {
        currentInfoEl.setAttribute('style', `${currentInfoEl.getAttribute('style')};margin-left: ${Math.abs(boxLeft)}px`);
        arrowEl.setAttribute('style', `margin-left: -${Math.abs(boxLeft)}px`);
      } else if ((boxLeft + boxNewWidth) > deviceWidth) {
        const disRight = deviceWidth - (boxLeft + boxNewWidth);
        currentInfoEl.setAttribute('style', `${currentInfoEl.getAttribute('style')};margin-left: -${Math.abs(disRight)}px`);
        arrowEl.setAttribute('style', `margin-left: ${Math.min(Math.abs(disRight), originBoxWidth / 2 - 6.5)}px`);
      }
      if ((boxTop - XHeight) < 0) { // 此处的18把X图标也算上
        // arrowEl.setAttribute
        // ('style', `${arrowEl.getAttribute('style')};bottom: ${boxHeight}px;border-color:rgba(255, 255, 255, 0) rgba(255, 255, 255, 0) rgba(0, 0, 0, 0.8) rgba(255, 255, 255, 0)`);
        // arrowEl.setAttribute('style', 'display:none');
        // currentInfoEl.setAttribute('style', `${currentInfoEl.getAttribute('style')};top: ${parseInt(currentInfoEl.style.top) + boxHeight + arrowHeight / 2 + signDotEl.height + 18}px`);
      }
      if (this.props.weatherStore.currentInfo.containerPoint.y < topDis) {
        arrowEl.setAttribute('style', `${arrowEl.getAttribute('style')};bottom: ${boxHeight}px;border-color:rgba(255, 255, 255, 0) rgba(255, 255, 255, 0) rgba(0, 0, 0, 0.8) rgba(255, 255, 255, 0)`);
        currentInfoEl.setAttribute(
          'style', `${currentInfoEl.getAttribute('style')};top: ${parseInt(this.props.weatherStore.currentInfo.containerPoint.y) + boxHeight + arrowHeight / 2 + signDotEl.height + 18}px`);
      }
    }
  }

  hideOverlayTip() {
    const temp = {
      hideOverlayTip: true
    };
    const infoList = Object.assign(this.props.weatherStore.currentInfo, temp);
    this.props.weatherStore.setCurrentInfo(toJS(infoList));
  }

  getTipArea() {
    let {currentInfo} = this.props.weatherStore;
    const {currentOverlayName, particleAnimationOpen} = this.props.weatherStore;
    currentInfo = toJS(currentInfo);
    if (currentInfo.hideOverlayTip) return null;
    if (JSON.stringify(currentInfo.containerPoint) === '{}') return null;
    if (currentInfo.formatOverlayInfo.windSpeedNum || currentInfo.formatOverlayInfo.heatNum) {
      if (particleAnimationOpen) {
        if (currentOverlayName === WIND_OVERLAY) {
          return (
            <div className='overlayTipArea' style={{left: `${currentInfo.containerPoint.x}px`, top: `${currentInfo.containerPoint.y}px`}}>
              <span
                className='xIcon'
                onClick={() => {
                  this.hideOverlayTip();
                }}
              />
              <span className='map_more map_more_size' onClick={() => { this.showWeatherPanelHandle(1); }} />
              <div className='overlayInfoBox'>
                <span>{`${infoUtil.getI18nOverlay(currentOverlayName)}: `}</span>
                <span>{`${infoUtil.getWindDirection(currentInfo.formatOverlayInfo.windDirectionAngle)} `}</span>
                <span>{infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.windSpeedNum)}</span>
                <span>{infoUtil.getUnit(currentOverlayName)}</span>
              </div>
              <span className='arrow' />
            </div>
          );
        }
        return (
          <div className='overlayTipArea' style={{left: `${currentInfo.containerPoint.x}px`, top: `${currentInfo.containerPoint.y}px`}}>
            <span
              className='xIcon'
              onClick={() => {
                this.hideOverlayTip();
              }}
            />
            <span className='map_more map_more_size' onClick={() => { this.showWeatherPanelHandle(1); }} />
            {/*
            只有风图层的时候，才展示当前位置的风速信息。其他图层即使开启了粒子动画，也不展示风速
            <div className='overlayInfoBox'>
              <span>{`${infoUtil.getI18nOverlay(WIND_OVERLAY)}: `}</span>
              <span>{`${infoUtil.getWindDirection(currentInfo.formatOverlayInfo.windDirectionAngle)} `}</span>
              <span>{infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.windSpeedNum)}</span>
              <span>{infoUtil.getUnit(WIND_OVERLAY)}</span>
            </div> */}
            <div className='overlayInfoBox'>
              <span>{`${infoUtil.getI18nOverlay(currentOverlayName)}: `}</span>
              <span>{infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.heatNum)}</span>
              <span>{infoUtil.getUnit(currentOverlayName)}</span>
            </div>
            <span className='arrow' />
          </div>
        );
      }
      if (currentOverlayName === WIND_OVERLAY) {
        return (
          <div className='overlayTipArea' style={{left: `${currentInfo.containerPoint.x}px`, top: `${currentInfo.containerPoint.y}px`}}>
            <span
              className='xIcon'
              onClick={() => {
                this.hideOverlayTip();
              }}
            />
            <span className='map_more map_more_size' onClick={() => { this.showWeatherPanelHandle(1); }} />
            <div className='overlayInfoBox'>
              <span>{`${infoUtil.getI18nOverlay(currentOverlayName)}: `}</span>
              <span>{`${infoUtil.getWindDirection(currentInfo.formatOverlayInfo.windDirectionAngle)} `}</span>
              <span>{infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.windSpeedNum)}</span>
              <span>{infoUtil.getUnit(currentOverlayName)}</span>
            </div>
            <span className='arrow' />
          </div>
        );
      }
      return (
        <div className='overlayTipArea' style={{left: `${currentInfo.containerPoint.x}px`, top: `${currentInfo.containerPoint.y}px`}}>
          <span
            className='xIcon'
            onClick={() => {
              this.hideOverlayTip();
            }}
          />
          <span className='map_more map_more_size' onClick={() => { this.showWeatherPanelHandle(1); }} />
          <div className='overlayInfoBox'>
            <span>{`${infoUtil.getI18nOverlay(currentOverlayName)}: `}</span>
            <span>{infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.heatNum)}</span>
            <span>{infoUtil.getUnit(currentOverlayName)}</span>
          </div>
          <span className='arrow' />
        </div>
      );
    }
    return (
      <div className='overlayTipArea nodata' style={{left: `${currentInfo.containerPoint.x}px`, top: `${currentInfo.containerPoint.y}px`}}>
        <span
          className='xIcon'
          onClick={() => {
            this.hideOverlayTip();
          }}
        />
        <div className='overlayInfoBox'>
          <span>no data</span>
        </div>
        <span className='arrow' />
      </div>
    );
  }

  getOverlayNum() {
    const {currentInfo, currentOverlayName, particleAnimationOpen} = this.props.weatherStore;
    let temp = null;
    if (currentInfo.formatOverlayInfo.windSpeedNum || currentInfo.formatOverlayInfo.heatNum) {
      if (particleAnimationOpen) {
        if (currentOverlayName === WIND_OVERLAY) {
          // 当前是风图层
          temp = (
            <div>
              {currentInfo.formatOverlayInfo.windDirectionAngle}
              <span> ° @ </span>
              {infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.windSpeedNum)}
              {` ${infoUtil.getUnit(currentOverlayName)}`}
            </div>
          );
        } else {
          temp = (
            <div>
              <div>
                {currentInfo.formatOverlayInfo.windDirectionAngle}
                <span> ° @ </span>
                {infoUtil.getFixedNum(WIND_OVERLAY, currentInfo.formatOverlayInfo.windSpeedNum)}
                {` ${infoUtil.getUnit(WIND_OVERLAY)}`}
              </div>
              <div>
                {infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.heatNum)}
                {` ${infoUtil.getUnit(currentOverlayName)}`}
              </div>
            </div>
          );
        }
      } else if (currentOverlayName === WIND_OVERLAY) {
        temp = null;
      } else {
        temp = (
          <div>
            {infoUtil.getFixedNum(currentOverlayName, currentInfo.formatOverlayInfo.heatNum)}
            {` ${infoUtil.getUnit(currentOverlayName)}`}
          </div>
        );
      }
    } else {
      temp = null;
    }
    return temp;
  }

  // 显示操作面板
  showOperationHandle() {
    this.props.weatherStore.setShowOperation(!this.props.weatherStore.showOperation);
  }

  showWeatherPanelHandle(statusNum) {
    this.hideOverlayTip();
    this.props.weatherStore.setShowInfoPanelStatus(statusNum);
  }

  render() {
    const {currentInfo, particleAnimationOpen, currentOverlayName} = this.props.weatherStore;
    return (
      <div className='CIContainer' ref={(container) => { this.container = container; }}>
        {/* 右上角当前图层，并且可以点击该按钮显示控制面板 */}
        <div className='fixedCurrentOverlay' onClick={this.showOperationHandle}>
          <div className={`overlayIcon ${currentOverlayName}Icon`} />
          <span className='overlayText'>{infoUtil.getI18nCurrentName(currentOverlayName)}</span>
        </div>
        {/* 当前点气泡信息 */}
        {this.getTipArea()}
        {/* 当前点图标 */}
        <div className='signDot' style={{display: `${currentInfo.coordinates.coordText ? 'flex' : 'none'}`, left: `${currentInfo.containerPoint.x}px`, top: `${currentInfo.containerPoint.y}px`}}>
          <div className='radius dot3' />
        </div>
        {/* 左下角区域 */}
        <div className='leftBottomArea' style={{display: 'none'}}>
          <div className='switchArea'>
            <div className={`${particleAnimationOpen ? 'switchIcon opened' : 'switchIcon hided'}`} onClick={() => this.props.changeParticleAnimationStatusFromParent(!particleAnimationOpen)} />
            <span className='switchText'>{i18n.PARTICLE_ANIMATION}</span>
          </div>
          <div className='infoBox' style={{display: `${currentInfo.coordinates.coordText ? 'block' : 'none'}`}}>
            <div className='coordBox'>{currentInfo.coordinates.coordText}</div>
            {this.getOverlayNum()}
          </div>
        </div>
      </div>
    );
  }
}
export default props => <CurrentInfo {...props} weatherStore={weatherStore} />;
