import 'whatwg-fetch';
import constants from './constants';
import {getAuthorization} from 'common/Utils'
// import { device, nativeHistory } from '@enos/envhybrid-utils';
// import uris from './uris';

const headers = {
  Accept: 'application/json',
  Authorization: getAuthorization(),
  'Content-Type': 'application/json'
};

function get(url) {
  headers.Authorization = getAuthorization();
  return fetch(url, {
    method: 'GET',
    headers,
    credentials: 'include'
  }).then(response => handleResponse(url, response)).catch((err) => {
    console.error(`Request failed. Url = ${url} . Message = ${err}`);
    return {error: {message: 'Request failed.'}};
  });
}

function post(url, data) {
  headers.Authorization = getAuthorization();
  return fetch(url, {
    method: 'POST',
    headers,
    body: JSON.stringify(data),
    credentials: 'include'
  }).then(response => handleResponse(url, response)).catch((err) => {
    console.error(`Request failed. Url = ${url} . Message = ${err}`);
    return {error: {message: 'Request failed.'}};
  });
}

function put(url, data) {
  headers.Authorization = getAuthorization();
  return fetch(url, {
    method: 'PUT',
    headers,
    body: JSON.stringify(data),
    credentials: 'include'
  }).then(response => handleResponse(url, response)).catch((err) => {
    console.error(`Request failed. Url = ${url} . Message = ${err}`);
    return {error: {message: 'Request failed.'}};
  });
}

function handleResponse(url, response) {
  if (response.status < 500) {
    if (response.status >= 400) {
      return;
    }
    return response.json();
  }
  // if(response.exception === "java.lang.IllegalArgumentException"){
  //   // 退出登录
  //   if (device.isMobile) {
  //     nativeHistory.logout();
  //   } else {
  //     window.location.href = uris.getLogout();
  //   }
  //   return;
  // }
  console.error(`Request failed. Url = ${url} . Message = ${response.statusText}`);
  return {error: {message: 'Request failed due to server error '}};
}
export {get, post, put};
