class AddressList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchValue: '',
      searchResult: [],
      isChinaArea: true // 国内和国外搜索接口切换使用，true表示国内。
    };
    this.isCompositions = true;
    this.loadAllItemClick = this._loadAllItemClick.bind(this);
    // this.search = this._search.bind(this);
    this._search = debounce(this._search, 500);
    this.onClear = this._onClear.bind(this);
    this.onCancel = this._onCancel.bind(this);
    // this.onFocus = this._onFocus.bind(this);
    // this.onBlur = this._onBlur.bind(this);
    document.addEventListener('navileft', this.onNaviLeft.bind(this), false);
    this.events = {
      onconfirm(e) { // 鼠标点击下拉列表后的事件
        const value = e.item.value;
        const myValue = value.province + value.city + value.district + value.street + value.business;
        const historyParam = `${value.business}&${myValue}`;
        this.setHistory(historyParam);
        const historyAddr = {name: value.business, addr: myValue};
        setStorage('fromAddrFc', historyAddr).then(() => {
          historyPush('/eos-fc-map/index.html#/windmap/0/0/addr');
        });
      },
      onhighlight() {
        // console.log('onhighlight');
      }
    };
  }

  goBack(geoInfo, locationAddr) {
    const historyParam = `${locationAddr.name}&${locationAddr.addr}&${geoInfo.lat}&${geoInfo.lon}`;
    this.setHistory(historyParam);
    const historyAddr = {lat: geoInfo.lat, lon: geoInfo.lon, name: locationAddr.name, addr: locationAddr.addr};
    this.props.weatherStore.setFromSearchStatus(true);
    this.props.weatherStore.setSelectedLocation(historyAddr);
    this.props.weatherStore.setShowInfoPanelStatus(1);
    const newInfo = Object.assign(toJS(this.props.weatherStore.currentInfo), {});
    const lonLat = {lat: geoInfo.lat, lng: geoInfo.lon};
    newInfo.hideOverlayTip = false;
    editAttributeFn('coordinates.latLng', lonLat, newInfo);
    this.props.weatherStore.setCurrentInfo(newInfo);
    this.props.history.push(`/weather/${constants.mode}`);
    // getStorage('fromEarth').then((data) => {
    //   if (data) { // 从earth中进入的
    //     setStorage('fromAddrFc', historyAddr).then(() => {
    //       historyPush('http://localhost:3000/screen.html');
    //     });
    //   } else {
    //     setStorage('fromAddrFc', historyAddr).then(() => {
    //       historyPush('/eos-fc-map/index.html#/windmap/0/0/addr');
    //     });
    //   }
    // });
  }

  componentDidMount() {
    this.searchFocus.focus();

    this.searchFocus.inputContainerRef.firstChild.children[1].addEventListener('compositionstart', this.comppsitionstart.bind(this)); // 非直接的文字输入时（键盘输入中文的拼音）
    this.searchFocus.inputContainerRef.firstChild.children[1].addEventListener('compositionend', this.compositionend.bind(this));// 直接输入文字后（键盘选择真实的汉字）
  }

  comppsitionstart() {
    this.isCompositions = false;
  }

  compositionend() {
    this.isCompositions = true;
  }

  handleMapMounted = () => {
    const BMap = window.BMap;// 取出window中的BMap对象
    const ac = new BMap.Autocomplete( // 建立一个自动完成的对象
      {
        input: 'suggestId'// this.refs.searchFocus.inputContainerRef.firstChild.children[1]
      });
    // 对百度自动联想出的地址添加ios兼容样式。
    setTimeout(() => {
      const listElmLen = document.getElementsByClassName('tangram-suggestion-main').length;
      if (listElmLen) {
        for (let i = 0; i < listElmLen; i++) {
          const curDom = document.getElementsByClassName('tangram-suggestion-main')[i];
          if (device.isiOS) {
            curDom.className += ' ios';
          }
        }
      }
    }, 200);
    let myValue;
    ac.addEventListener('onconfirm', (e) => { // 鼠标点击下拉列表后的事件
      const value = e.item.value;
      myValue = value.province + value.city + value.district + value.street + value.business;
      const historyParam = `${value.business}&${myValue}`;
      this.setHistory(historyParam);
      const historyAddr = {name: value.business, addr: myValue};
      getStorage('fromEarth').then((data) => {
        if (data) { // 从earth中进入的
          setStorage('fromAddrFc', historyAddr).then(() => {
            // console.log('fromAddrFc: ', historyAddr);
            // historyPush('http://localhost:3000/screen.html');
            historyPush('/eos-fc-map/screen.html');
          });
        } else {
          setStorage('fromAddrFc', historyAddr).then(() => {
            // console.log('fromAddrFc: ', historyAddr);
            historyPush('/eos-fc-map/index.html#/windmap/0/0/addr');
          });
        }
      });
      // setStorage('fromAddrFc',historyAddr).then(()=>{
      //   historyPush('/eos-fc-map/index.html#/windmap/0/0/addr');
      // });

      // sharedDataApi.saveShellPersistentData('fromAddressFC', historyParam).then(()=>{
      //   nativeHistory.push('/eos-fc-map/index.html#/windmap/0/0/addr');
      // });
    });
  }

  setHistory(historyParam) {
    // 使用工具函数进行存储
    if (historyParam !== '') {
      // 写入a字段
      let isHave = false;
      getStorageUnparse(addressHistory).then((historyResult) => {
        if (historyResult !== undefined) {
          historyResult.split('##').forEach((n) => {
            if (n === historyParam) {
              isHave = true;
            }
          });
        } else {
          setStorageUnSty(addressHistory, '');
        }
        if (!isHave) {
          if (historyResult !== undefined) {
            if (historyResult !== '') {
              setStorageUnSty(addressHistory, `${historyResult}##${historyParam}`);
            } else {
              setStorageUnSty(addressHistory, historyParam);
            }
          } else {
            setStorageUnSty(addressHistory, historyParam);
          }
        }
      });
    }

    // 本地进行存储
    // if (!window.localStorage) {
    //   alert('浏览器不支持localstorage');
    //   return false;
    // }
    // if (historyParam !== '') {
    //   const storage = window.localStorage;
    //   // 写入a字段
    //   let isHave = false;
    //   if (storage[addressHistory] !== undefined) {
    //     storage[addressHistory].split('##').forEach((n) => {
    //       if (n === historyParam) {
    //         isHave = true;
    //       }
    //     });
    //   } else {
    //     storage[addressHistory] = [];
    //   }
    //   if (!isHave) {
    //     if (storage[addressHistory] !== undefined) {
    //       if (storage[addressHistory] !== '') {
    //         storage[addressHistory] += `##${historyParam}`;
    //       } else {
    //         storage[addressHistory] = historyParam;
    //       }
    //     } else {
    //       storage[addressHistory] = historyParam;
    //     }
    //   }
    // }
  }

  onNaviLeft() {
    historyBack();
  }

  _loadAllItemClick() {
    this.setState({searchValue: ''});
  }

  _noContentHandle = () => {
    this.setState({searchResult: []});
    // 内容清空的同时，防止还有pending的请求，将之前的请求也给cancel。
    axiosUtil({
      url: `${uris.getSearch()}q=*&zoom=18&addressdetails=1&accept-language=en-US`,
      method: 'get'
    });
  }

  _search = (searchValue) => {
    setTimeout(() => {
      if (this.isCompositions) { // 输入的是拼音就不回调输入的内容
        if (searchValue === '') {
          this._noContentHandle();
          return;
        }
        let url;
        if (!this.state.isChinaArea) { // 全英文
          url = `${uris.getSearch()}q=${searchValue}&zoom=18&addressdetails=1&accept-language=en-US`;
        } else { // 中文搜索
          url = `${uris.getAddressByGaode()}${searchValue}`;
        }
        axiosUtil({
          url,
          method: 'get'
        }).then((res) => {
          const data = res.data;
          this.setState({searchResult: data});
        }).catch((error) => {
          console.log(error);
          this.setState({searchResult: []});
        });
      }
    }, 100);// 延迟回调模块中input中输入的值

    // if (lan) {
    //   get(`${uris.getSearch()}q=${searchValue}&zoom=18&addressdetails=1&accept-language=en-US`).then(r => {
    //     if (r.error) {
    //       this.setState({ searchResult: [] })
    //     } else {
    //       this.setState({ searchResult: r })
    //     }
    //   }).catch(() => {
    //     this.setState({ searchResult: [] })
    //   })
    // } else {
    //   get(`${uris.getSearch()}q=${searchValue}&zoom=18&addressdetails=1&accept-language=zh-CN`).then(r => {
    //     if (r.error) {
    //       this.setState({ searchResult: [] })
    //     } else {
    //       this.setState({ searchResult: r })
    //     }
    //   }).catch(() => {
    //     this.setState({ searchResult: [] })
    //   })
    // }
  }

  searchChange = (searchValue) => {
    if (searchValue === '') {
      this._noContentHandle();
    }
    // let reg=/^[\u4e00-\u9fa5]+$/; // 匹配中文
    const reg = /^[a-zA-Z]+$/; // 匹配英文
    const normalizeVal = searchValue.replace(/\s+/g, '');
    this.setState({searchValue, isChinaArea: !reg.test(normalizeVal)}, () => {
      this._search(this.state.searchValue);
    });
  }

  _onClear() {
    this.setState({
      searchValue: ''
    });
  }

  _onCancel() {
    this.props.history.goBack();
  }

  _onBlur() {
    // this.searchFocus.focus();
  }

  render() {
    return (
      <div className={`farmlist${device.isiOS ? ' ios' : ''}`}>
        {/* <input type="text" id="suggestId" readOnly value='none' style={{display:'none'}} /> */}
        <div className='searchArea'>
          <SearchBar
            showCancelButton
            placeholder={i18n.LOCATION_SEARCH}
            value={this.state.searchValue}
            onChange={this.searchChange}
            onClear={this.onClear}
            onCancel={this.onCancel}
            // onFocus={this.onFocus}
            // onBlur={this.onBlur}
            cancelText={i18n.CANCEL}
            ref={(ref) => { this.searchFocus = ref; }}
          />
        </div>
        {this.getResultInEn()}
        {/* <div style={{display:'none'}}>
          <Map
            ak={uris.getBaiduAk()}
            mapMounted={this.handleMapMounted}>
          </Map>
        </div> */}
        {/* 下面是之前用来匹配地址的方式，英文环境下使用的方法能够正确搜索到国外地区的位置，所以统一使用接口查询地址，不是用百度地图的实例方法 */}
        {/* {lan?this.state.searchResult.map(geoInfo=>{
          let poisName = '';
          let poisAddr = '';
          if(geoInfo.display_name){
            const add=geoInfo.display_name.split(",");
            for (let i=1;i<add.length-1&&i<4;i++){
              poisAddr+=(add[i]+" ");
            }
            poisName=add[0];
          }
          let locationAddr = {
            "name":geoInfo.address&&geoInfo.address.building || poisName || geoInfo.result.formatted_address,
            "addr": geoInfo.display_name || poisAddr
          };
          return
            <div className="addItem" onClick={()=>this.goBack(geoInfo,locationAddr)}>
              <div className="icon"/><div className="addName">{locationAddr.name}</div><div className="addLoc">{locationAddr.addr}</div></div>
        }):
        <div style={{display:'none'}}>
          <Map
            ak={uris.getBaiduAk()}
            mapMounted={this.handleMapMounted}>
          </Map>
        </div>
        } */}
      </div>
    );
  }

  getResultInEn = () => {
    let result = [];
    if (!this.state.isChinaArea) { // 英文结果格式化
      result = (Array.isArray(this.state.searchResult) ? this.state.searchResult : []).map((geoInfo, index) => {
        let poisName = '';
        let poisAddr = '';
        if (geoInfo.display_name) {
          const add = geoInfo.display_name.split(',');
          for (let i = 1; i < add.length - 1 && i < 4; i++) {
            poisAddr += (`${add[i]} `);
          }
          poisName = add[0];
        }
        const locationAddr = {
          name: (geoInfo.address && geoInfo.address.building) || poisName || geoInfo.result.formatted_address,
          addr: geoInfo.display_name || poisAddr
        };
        return (
          <div className='addItem' onClick={() => this.goBack(geoInfo, locationAddr)} key={index}>
            <div className='icon' />
            <div className='addName'>{locationAddr.name}</div>
            <div className='addLoc'>{locationAddr.addr}</div>
          </div>
        );
      });
    } else { // 中文结果格式化
      result = (this.state.searchResult.tips || []).map((item) => {
        const locationAddr = {
          name: item.name || '--',
          addr: (item.district + item.address) || '--'
        };
        const deg = ((item.location && item.location.length) > 0) ? item.location.split(',') : ['', ''];
        const locationInfo = {
          lon: deg[0],
          lat: deg[1]
        };
        return (
          <div className='addItem' onClick={() => this.goBack(locationInfo, locationAddr)} key={item.id}>
            <div className='icon' />
            <div className='addName'>{locationAddr.name}</div>
            <div className='addLoc'>{locationAddr.addr}</div>
          </div>
        );
      });
    }

    return (
      <div className={`resultContainer${device.isiOS ? ' ios' : ''}`}>
        {this.state.searchValue === '' || (result && result.length > 0)
          ? result
          : (
            <div className='search-notfound'>
              <i className='icon' />
              <div className='noSearchContent'>{i18n.NOSEARCHRESULT}</div>
              <div className='inputAgain'>{i18n.INPUTAGAIN}</div>
            </div>
          )
        }
      </div>
    );
  }
}

export default props => <AddressList {...props} weatherStore={weatherStore} />;
