  const url = window.location.href;
  const keys = Object.keys(modes);
  let curMode = '';
  keys.forEach((v) => {
    if (url.indexOf(v) > -1 || url.indexOf(v.toLocaleLowerCase()) > -1) {
      curMode = v;
    }
  });
  if (!curMode) {
    curMode = modes.GLOBAL;
  }
