import i18n from 'common/i18n';
import uris from 'common/utils/uris';
import {get} from 'common/utils/request';
import {WIND_OVERLAY, TEMP_OVERLAY, HUMIDITY_OVERLAY, RAIN_OVERLAY} from '../../pages/Weather/constants';

export function getI18nTitle(overlayName) {
  switch (overlayName) {
  case WIND_OVERLAY:
    return i18n.WIND_FORECAST;
  case TEMP_OVERLAY:
    return i18n.TEMP_FORECAST;
  case HUMIDITY_OVERLAY:
    return i18n.HUMIDITY_FORECAST;
  case RAIN_OVERLAY:
    return i18n.PRECIPITATION_FORECAST;
  default:
    return '';
  }
}

export function getDataRequestUrl(overlayName, params, isGlobal = true) {
  let url = '';
  let requestList = [];
  const weatherSource = uris.getNewWeatherSource();
  switch (overlayName) {
  case WIND_OVERLAY:
    url = `${weatherSource}WD10${isGlobal ? '0' : ''}${params}`;
    requestList = [get(url), get(`${weatherSource}WS10${isGlobal ? '0' : ''}${params}`)];
    return requestList;
  case TEMP_OVERLAY:
    url = `${weatherSource}T2${params}`;
    requestList.push(get(url));
    return requestList;
  case HUMIDITY_OVERLAY:
    url = `${weatherSource}RH2${params}`;
    requestList.push(get(url));
    return requestList;
  case RAIN_OVERLAY:
    url = `${weatherSource}RAINFALL${params}`;
    requestList.push(get(url));
    return requestList;
  default:
    return '';
  }
}

export function getEchartUnit(overlayName) {
  switch (overlayName) {
  case TEMP_OVERLAY:
    return '°C';
  case HUMIDITY_OVERLAY:
    return '%';
  case RAIN_OVERLAY:
    return 'mm';
  default:
    return '';
  }
}
